<script>
import All from "@/components/SearchNav/All.vue";
import Album from "@/components/SearchNav/Album.vue";
import Sound from "@/components/SearchNav/Sound.vue";
import Anchor from "@/components/SearchNav/Anchor.vue";
export default {
  props: ["anchor"],
  components: { All, Album, Sound, Anchor },
  data: function () {
    return {
      value: this.$route.params.kw,
      albums: Object,
      albumdetails: {},
      active: 0,
    };
  },
  created: function () {
    this.$route.params.kw = document.querySelector("input").value;
    this.axios
      .get(
        "https://apis.netstart.cn/ximalaya/search?kw=" +
          this.value +
          "&core=all&page=1&rows=20"
      )
      .then((res) => {
        this.albums = res.data.data;
        // console.log(this.albums);
        // console.log(res);
        this.albumdetails = this.albums.albumViews.albums;
      });
  },
  methods: {
    onSearch(val) {
      console.log(val);
    },
    onCancel() {
      // this.value = "";
      this.go(-1);
    },
    count: function (value) {
      return value > 10000 ? (value / 10000).toFixed(2) + "万" : value;
    },
    transferVal: function (event) {
      this.$router.push({
        name: "AnchorDetail",
        params: {
          id: event,
        },
      });
    },
  },
  watch: {
    $route(to, from) {
      to, from;
      this.axios
        .get(
          "https://apis.netstart.cn/ximalaya/search?kw=" +
            this.value +
            "&core=all&page=1&rows=20"
        )
        .then((res) => {
          this.albums = res.data.data;
          // console.log(this.albums);
          // console.log(res);
          this.albumdetails = this.albums.albumViews.albums;
        });
    },
  },
};
</script>

