import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import About from '../views/About.vue'
import Search from '../views/Search.vue'
import Album from '../views/Album.vue'
import Sound from '../views/Sound.vue'
import Zhubo from '../views/Zhubo.vue'
import SearchDetail from '../views/SearchDetail.vue'
import YouShengShu from '../views/YouShengShu.vue'
import XiangSheng from '../views/XiangSheng.vue'
import YinYue from '../views/YinYue.vue'
import ErTong from '../views/ErTong.vue'
import TouTiao from '../views/TouTiao.vue'
import RenWen from '../views/RenWen.vue'
import QingGan from '../views/QingGan.vue'
import LiShi from '../views/LiShi.vue'
import KeJi from '../views/KeJi.vue'
import Category from '../views/Category.vue'
import Top from '../views/Top.vue'
import Play from '../views/Play.vue'
import DownLoad from '../views/DownLoad.vue'
import AnchorDetail from '../views/AnchorDetail.vue'

Vue.use(VueRouter)

