// 创建router实例
// 传入routes配置
const routes =
  [
    {
      path: "/",
      redirect: "/home",
    },
    {
      path: '/home',
      component: Home,
      meta: {
        showNav: true,
      },
      children: [
        {
          path: 'youshengshu',
          // component: YouShengShu,
          component: () => import(/* webpackChunkName: "home" */ '../views/YouShengShu.vue'),
          meta: {
            showNav: true,
          },
        },
        {
          path: 'xiangsheng',
          component: XiangSheng,
          // component: () => import(/* webpackChunkName: "about" */ '../views/XiangSheng.vue'),
          meta: {
            showNav: true,
          },
        },
        {
          path: 'yinyue',
          component: YinYue,
          meta: {
            showNav: true,
          },
        },
        {
          path: 'ertong',
          component: ErTong,
          meta: {
            showNav: true,
          },
        },
        {
          path: 'toutiao',
          component: TouTiao,
          meta: {
            showNav: true,
          },
        },
        {
          path: 'renwen',
          component: RenWen,
          meta: {
            showNav: true,
          },
        },
        {
          path: 'qinggan',
          component: QingGan,
          meta: {
            showNav: true,
          },
        },
        {
          path: 'lishi',
          component: LiShi,
          meta: {
            showNav: true,
          },
        },
        {
          path: 'keji',
          component: KeJi,
          meta: {
            showNav: true,
          },
        },

      ],
    },
    {
      path: "/youshengshu",
      component: YouShengShu,
    },
    {
      path: '/search',
      name: Search,
      component: Search,


    },
    {
      path: '/search',
      name: 'SearchDetail',
      component: SearchDetail,
      children: [
        {
          path: 'album/:kw',
          name: 'Album',
          component: Album
        },
        {
          path: 'sound/:kw',
          name: 'Sound',
          component: Sound
        },
        {
          path: 'zhubo/:kw',
          name: 'Zhubo',
          component: Zhubo
        }
      ]
    },
    {
      path: '/category',
      component: Category
    },
    {
      path: '/top/:clusterCode/:categoryCode',
      name: 'Top',
      component: Top,
      meta: {
        showNav: true,
      },
    },
    {
      path: '/:clusterCode/:categoryCode/:id',
      name: 'Play',
      component: Play,
    },
    {
      path: '/:categoryCode/:id',
      name: 'About',
      // route level code-splitting
      // this generates a separate chunk (about.[hash].js) for this route
      // which is lazy-loaded when the route is visited.
      // component: () => import(/* webpackChunkName: "about" */ '../views/About.vue')
      component: About,

    },
    {
      path: '/download',
      component: DownLoad
    },
    {
      path: "/zhubo/:id",
      name: "AnchorDetail",
      component: AnchorDetail,
    },
  ]
const router = new VueRouter({
  routes
})

export default router