<template>
  <div class="other">
    <ul class="cate-wrap">
      <a v-for="item in tomatoes" :key="item.order" class="con">
        <img :src="item.img" alt="" />
        <a href="#">{{ item.name }}</a>
      </a>
    </ul>
    <a href="#" class="advertise">
      <img src="../assets/ximalaya.png" alt="" />
    </a>

    <div class="nav-list" v-for="item in list" :key="item.id">
      <h2 class="title">{{ item.moduleInfo.displayName }}</h2>
      <div class="grid">
        <div
          class="card"
          v-for="card in item.albumBriefDetailInfos.slice(0, 3)"
          :key="card.id"
        >
          <a href="#">
            <div class="album-cover">
              <img :src="card.albumInfo.cover" alt="" />
              <div class="mask">
                <div class="info-bar">
                  <span class="icon">
                    <!-- <i class="xm-icon icon-play_ic_shound"></i> -->
                  </span>
                  <span class="count">{{
                    count(card.statCountInfo.playCount)
                  }}</span>
                </div>
              </div>
            </div>

            <div class="desc">
              <p>{{ card.albumInfo.title }}</p>
            </div>
          </a>
        </div>
      </div>
      <div class="grid-bottom">
        <div
          class="grid-item"
          v-for="card in item.albumBriefDetailInfos.slice(3, 6)"
          :key="card.id"
        >
          <div class="left">
            <img :src="card.albumInfo.cover" alt="" />
          </div>
          <div class="right">
            <h3>{{ card.albumInfo.title }}</h3>
            <p>{{ card.albumInfo.shortIntro }}</p>
            <div class="icon-cover">
              <span class="count">
                <i class="xm-icon icon-sound"></i>
                {{ card.statCountInfo.trackCount }}
              </span>
              <span class="ear-phone">
                <i class="xm-icon icon-data"></i>
                {{ (card.statCountInfo.playCount / 10000).toFixed(2) + "万" }}
              </span>
            </div>
          </div>
        </div>
      </div>
    </div>

   <MoreTuijian></MoreTuijian>

    <div class="footer">
      <div class="logo"></div>
      <div class="open">
        <div class="content">
          <span>打开APP,听更多声音</span>
          <i class="xm-icon icon-album_btn_more"></i>
        </div>
      </div>

      <div class="copyright">
        <p>© 2014-2021 ximalaya.com 版权所有</p>
      </div>
      <p class="forbid">未经授权，禁止商用，喜马拉雅保留追究法律责任的权利</p>
    </div>
  </div>
</template>

<script>
import MoreTuijian from "@/components/MoreTuijian.vue";
export default {
  props: ["cards", "details"],
  components:{MoreTuijian},
  data: function () {
    return {
      showProgram: true,
      list:[],
      tomatoes:[],
      morelist:[]
    };
  },
  created: function () {
     this.axios
      .get(
        "https://m.ximalaya.com/m-revision/page/index/queryIndexTabContent?moduleKey=keji"
      )
      .then((res) => {
        setTimeout(() => {
          this.tomatoes = res.data.data.moduleContent.tomatoes;
        });
      }, 2000);
    this.axios
      .get(
        "https://apis.netstart.cn/ximalaya/index/queryIndexCategoryTabContent?moduleKey=keji"
      )
      .then((res) => {
        res;
        setTimeout(() => {
          this.list = res.data.data.moduleContent.moduleRankDatas;
          // this.list = res.data.data;
          // console.log("111", this.list);
        },2000);

        });
         this.axios
      .get(
        "https://m.ximalaya.com/m-revision/page/index/queryCategoryFeed?moduleKey=keji"
      )
      .then((res) => {
        res;
        setTimeout(() => {
          this.morelist = res.data.data;
          // this.list = res.data.data;
          // console.log("111", res);
        },2000)
      });
  },
  methods: {
    count: function (n) {
      return n > 100000000
        ? (n / 100000000).toFixed(2) + "亿"
        : (n / 10000).toFixed(2) + "万";
    },
  },
};
</script>
<style lang="less" scoped>
.other {
  margin-top: 25px;
  .cate-wrap {
    width: 310px;
    margin: 0 auto;
    display: flex;
    text-align: center;

    .con {
      flex: 1;
      display: flex;
      flex-direction: column;
      align-items: center;
      img {
        width: 34px;
        height: 34px;
        display: inline-block;
        border-radius: 50%;
      }
      a {
        text-align: center;
        text-decoration: none;
        font-size: 12px;
        color: #666;
        text-align: center;
        height: 17px;
        line-height: 17px;
      }
    }
  }

  .advertise {
    color: #7e8c8d;
    text-decoration: none;

    img {
      max-width: 100%;
      border: none;
      font-size: 10px;
      margin: 18px 0 8px 0;
      overflow: hidden;
    }
  }

  .nav-list {
    // background:pink;
    .title {
      font-size: 18px;
      color: #40404c;
      line-height: 25px;
      margin: 17px 0 17px 15px;
      font-weight: 700;
      text-align: left;
    }
    .grid {
      padding: 0 15px;
      display: flex;

      .card {
        display: inline-block;
        margin: 0 3px;
        width: 100%;

        a {
          display: block;

          .album-cover {
            padding-bottom: 100%;
            height: 0;
            overflow: hidden;
            position: relative;
            background-image: url(../assets/mask.png);
            background-repeat: no-repeat;
            background-size: contain;
            background-position: center;

            img {
              width: 100%;
              // height: 100%;
              border-radius: 4px;
              max-width: 100%;
              border: none;
              font-size: 10px;
            }

            .mask {
              position: absolute;
              left: 0;
              // top: 0;
              right: 0;
              bottom: 0;

              .info-bar {
                background-image: linear-gradient(
                  180deg,
                  rgba(3, 3, 3, 0) 9%,
                  rgba(0, 0, 0, 0.58) 100%
                );
                width: 100%;
                height: 28px;
                line-height: 34px;
                border-radius: 0 0 4px 4px;
                bottom: 0;
                left: 0;
                font-size: 12px;
                line-height: 17px;
                color: #fff;
                letter-spacing: 0.34px;
                vertical-align: middle;

                .count {
                  text-align: left;
                }
              }
            }
          }

          .desc {
            padding: 6px 0 11px 0;
            font-size: 13px;
            line-height: 18px;
            height: 53px;
            overflow: hidden;
            box-sizing: border-box;
            color: #333;
            letter-spacing: 0.34px;

            p {
              display: -webkit-box;
              overflow: hidden;
              -webkit-line-clamp: 2;
              text-overflow: ellipsis;
              -webkit-box-orient: vertical;
            }
          }
        }
      }
    }
    .grid-bottom {
      margin: 0 15px;
      .grid-item {
        padding: 15px 10px 15px 0;
        box-sizing: border-box;
        // display: flex;
        .left {
          width: 94px;
          height: 94px;
          float: left;
          padding: 0 15px 0 0;
          background-color: #fff;
          box-sizing: content-box;
          margin-bottom: -1px;
          background-image: url(../assets/mask.png);
          background-position: center;
          background-repeat: no-repeat;
          background-size: contain;
          img {
            max-width: 100%;
            border: none;
            font-size: 10px;
            width: 100%;
            height: 100%;
            border-radius: 4px;
          }
        }
        .right {
          overflow: hidden;
          h3 {
            color: #333;
            line-height: 1.38;
            font-weight: normal;
            font-size: 16px;
            overflow: hidden;
            white-space: nowrap;
            text-overflow: ellipsis;
            text-align: left;
          }
          p {
            height: 34px;
            overflow: hidden;
            display: -webkit-box;
            -webkit-line-clamp: 2;
            text-overflow: ellipsis;
            margin: 5px 0 7px;
            font-size: 13px;
            line-height: 1.38;
            color: #999;
            text-align: left;
          }
          .icon-cover {
            font-size: 11px;
            line-height: 1.45;
            white-space: nowrap;
            color: #999;
            text-align: left;
            span {
              display: inline-block;
              margin-right: 10px;
              line-height: 20px;
              vertical-align: top;
              min-width: 55px;
            }
          }
        }
      }
    }
  }

  .footer {
    border-top: 0.5px solid #eee;
    max-width: 640px;
    min-width: 320px;
    margin: auto;
    height: 315px;
    background: url(../assets/footer.png) no-repeat center;
    background-size: cover;

    .logo {
      width: 100%;
      height: 36px;
      background: url(../assets/footer_logo.png) no-repeat center;
      background-size: 282px 36px;
      margin: 34px auto 0;
    }
    .open {
      width: 300px;
      display: flex;
      justify-content: space-around;
      font-size: 12px;
      margin: 7px auto 10px;
      text-align: center;
      .content {
        width: 292px;
        height: 49px;
        border-radius: 25px;
        border: 1px solid #fa2800;
        color: #fa2800;
        span {
          font-size: 18px;
          font-weight: normal;
          color: #fa2800;
          display: inline-block;
          line-height: 50px;
          margin-right: 8px;
        }
        .icon-album_btn_more {
          font-size: 16px;
        }
      }
    }
    .copyright {
      padding-bottom: 10px;
      font-size: 12px;
      font-weight: 400;
      color: gray;
      line-height: 16px;
      text-align: center;
      margin-top: 15px;
    }
    .forbid {
      font-size: 12px;
      text-align: center;
      color: grey;
      line-height: 12px;
      margin-bottom: 20px;
    }
  }
}
</style>