<template>
  <div id="app">
      <div class="header" v-if="$route.meta.showNav">
       
        <div class="header-search">
          <a href="#" class="logo-icon">
            <i class="xm-icon icon-logo"></i>
          </a>
          <div class="search-box">
            <div class="search">
              <i class="xm-icon icon-searchlight"></i>
              <input
                type="text"
                placeholder="搜索"
                @click="$router.push('/search')"
              />
            </div>
          </div>
          <a class="open-app" @click="$router.push('/download')">打开APP</a>
        </div>
      </div>
       
        <!-- <Loading v-if="!details.length"></Loading> -->
      <div
        class="myNav"
        v-if="$route.meta.showNav"
        :class="[is_fixed ? 'no-fixed' : 'is-fixed']"
      >
        <div class="nav">
          <ul>
            <van-tabs
              sticky
              v-model="active"
              swipeable
              van-tab-class="my-active"
              @change="onChange"
              line-width="18px"
            >
              <van-tab
                v-for="(item, index) in category"
                :title="item.moduleName"
                :key="index"
                :to="{
                  path:
                    item.moduleKey === 'tuijian'
                      ? '/home'
                      : `/home/${item.moduleKey}`,
                }"
                :name="item.moduleKey"
              >
              </van-tab>
            </van-tabs>
          </ul>
        </div>
      </div>
      <!-- 路由出口 -->
      <!-- <keep-alive> -->
      <!-- <transition
      name="custom-classes-transition"
      enter-active-class="animate__animated animate__zoomInDown"
    > -->
      <router-view
        :tomatoes="tomatoes"
        :details="details"
        :cards="cards"
        @transfer-value="transferValue"
        @current-play-program="currentPlayProgram = $event"
        :currentPlayProgram="currentPlayProgram"
        @update:current-play-program="currentPlayProgram = $event"
        :currentPlayProgramId="currentPlayProgramId"
        @jump-about-page="jumpAboutpage"
        @update:playlist="playlist = $event"
        :playlist="playlist"
        @transfer-works="works = $event"
        :works="works"
        @transfer-albums="albums = $event"
        :albums="albums"
        @transfer-worker="worker = $event"
        :worker="worker"
        @send-value="sendValue"
        @transfer-myalbums="myalbums = $event"
        :myalbums="myalbums"
        @transfer-classic="classic = $event"
        :classic="classic"
        @transfer-anchor="anchor = $event"
        :anchor="anchor"
        @translate-val="translateVal"
      ></router-view>
      <!-- </transition> -->
      <!-- </keep-alive> -->
      <!-- <van-loading size="24px">加载中...</van-loading> -->
    
  </div>
</template>
<script>
// import Loading from "@/components/Loading.vue";
export default {
  // components: { Loading },
  data: function () {
    return {
      active: "",
      category: [],
      tomatoes: {},
      details: [],
      cards: [],
      currentPlayProgram: {},
      playlist: [],
      works: Object,
      albums: Array,
      worker: Object,
      myalbums: Array,
      classic: Array,
      anchor: [String, Number],
      scrollTop: "",
      is_fixed: true,
      show: false,
      loading:true,
      animate:true,
      arr:[]
    };
  },
  provide() {
    return {
      reload: this.reload,
    };
  },
  created: function () {
    // console.log(this.arr);
    this.axios
      .get("https://m.ximalaya.com/m-revision/page/index/queryIndexTabModule")
      .then((res) => {
        setTimeout(() => {
          this.category = res.data.data;
          // console.log(this.category);
        }, 3000);
      });

    // var arr = this.$route.fullPath.split("/") || "tuijian";

    // console.log(this.$route.fullPath.split("/"));
    if (this.$route.path === "/home") {
      // 首页推荐列表
      this.axios
        .get(
          "https://m.ximalaya.com/m-revision/page/index/queryIndexTabContent?moduleKey=tuijian"
        )
        .then((res) => {
          setTimeout(() => {
            this.tomatoes = res.data.data.moduleContent.tomatoes;
            this.details = res.data.data.moduleContent.moduleRankDatas;
            this.cards = this.details.map((item) => {
              return item.rankingContentInfoList;
            });
          });
        }, 3000);
    }

    // 首页其他列表
    //   if (this.$route.fullPath !== "/home") {
    //     this.axios
    //       .get(
    //         "https://apis.netstart.cn/ximalaya/index/queryIndexCategoryTabContent?moduleKey=" +
    //           arr[arr.length - 1])
    //       .then((res) => {
    //         res
    //         this.list = res.data.data.moduleContent.moduleRankDatas;
    //         // this.list = res.data.data;
    //         console.log("111",this.list);
    //       });
    //   }
    // },
  },
  mounted: function () {
    window.addEventListener("scroll", this.handleScroll);
    this.loading = false;
        

  },
  destroyed() {
    window.removeEventListener("scroll", this.handleScroll);
  },
  methods: {
    onChange: function (event) {
      this.active = event;
    },
    transferValue: function (event) {
      this.$router.push({
        name: "About",
        params: {
          categoryCode: event.categoryCode,
          id: event.id,
        },
      });
      // console.log(event);
    },
    jumpAboutpage: function (event) {
      this.$router.push({
        name: "About",
        params: {
          categoryCode: event.pageUriInfo.categoryCode,
          id: event.pageUriInfo.id,
        },
      });
    },
    sendValue: function (event) {
      this.$router.push({
        name: "About",
        params: {
          categoryCode: event.categoryCode,
          id: event.id,
        },
      });
    },
    translateVal: function (event) {
      this.$router.push({
        name: "About",
        params: {
          categoryCode: event.categoryCode,
          id: event.id,
        },
      });
    },
    handleScroll: function () {
      // 滚动条偏移量
      let scrollTop =
        window.pageYOffset ||
        document.documentElement.scrollTop ||
        document.body.scrollTop;
      // 要滚动到顶部吸附的元素的偏移量
      let offsetTop = document.querySelector(".header").offsetTop;
      this.isFixed = offsetTop > scrollTop ? true : false;
    },
    // 没用
    // routerRefresh:function(){
    //   this.routerAlive = false;
    //   this.$nextTick(() => {
    //     this.routerAlive = true;
    //   })
    // }
  },
  // 使用watch监听$route的变化(响应路由参数的变化)
  watch: {
    $route(to, from) {
      console.log(to.fullPath, from.fullPath);
      // var arr = this.$route.fullPath.split("/") || "tuijian";
      this.axios
        .get(
          "https://m.ximalaya.com/m-revision/page/index/queryIndexTabContent?moduleKey=tuijian" 
        )
        .then((res) => {
          this.tomatoes = res.data.data.moduleContent.tomatoes;
          this.details = res.data.data.moduleContent.moduleRankDatas;
          this.cards = this.details.map((item) => {
            return item.rankingContentInfoList;
          });
        });
    },
  },
  // 计算当前播放节目的id
  computed: {
    currentPlayProgramId: function () {
      if (!this.currentPlayProgram) return "";
      return this.currentPlayProgram.id;
    },
  },
};
</script>
