<template>
  <div class="bottom-list">
    <p class="album-title">更多推荐</p>
    <div class="scroll_wrapper">
      <div class="content">
        <section class="list-view">
          <div
            class="xm-album"
            v-for="(item, index) in morelist.materials"
            :key="index"
          >
            <div class="left-cover">
              <img
                :src="'https://imagev2.xmcdn.com/' + item.data.albumInfo.cover"
                alt=""
              />
              <div class="cover-panel">
                <div class="mask"></div>
                <div class="play-btn"></div>
              </div>
            </div>
            <div class="right-content">
              <h3 class="title">{{ item.data.albumInfo.title }}</h3>
              <p class="sub-title">{{ item.data.albumInfo.salePoint }}</p>
              <div class="album-info">
                <span>
                  <i class="xm-icon icon-sound"></i>
                  {{ item.data.statCountInfo.trackCount }}
                </span>
                <span>
                  <i class="xm-icon icon-data"></i>
                  {{ count(item.data.statCountInfo.playCount) }}
                </span>
              </div>
            </div>
          </div>
        </section>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data: function () {
    return {
      morelist: [],
    };
  },
  created: function () {
    var arr = this.$route.path.split("/").splice(2, 1).toString();
    //   console.log(arr);
    this.axios
      .get(
        "https://m.ximalaya.com/m-revision/page/index/queryCategoryFeed?moduleKey=" +
          arr
      )
      .then((res) => {
        res;
        setTimeout(() => {
          this.morelist = res.data.data;
          // this.list = res.data.data;
          // console.log("111", res);
        }, 3000);
      });
  },
  methods: {
    count: function (n) {
      return n > 100000000
        ? (n / 100000000).toFixed(2) + "亿"
        : (n / 10000).toFixed(2) + "万";
    },
  },
};
</script>

<style lang="less" scoped>
.bottom-list {
  margin: 0 16px;
  .album-title {
    font-size: 18px;
    color: #40404c;
    line-height: 25px;
    margin: 17px 0 17px 0;
    font-weight: 700;
    text-align: left;
  }
  .scroll_wrapper {
    // height: 100vh;
    .content {
      position: relative;
      .list-view {
        position: relative;
        display: block;
        z-index: 10;
        height: 100%;
        transition: all 0.1s;

        .xm-album {
          padding: 15px 10px 15px 0;
          box-sizing: border-box;
          min-height: 120px;
          // float: left;
          .left-cover {
            width: 94px;
            height: 94px;
            float: left;
            padding: 0 15px 0 0;
            background-color: #fff;
            box-sizing: content-box;
            margin-bottom: -1px;
            position: relative;
            max-height: 100%;
            // background-image: url(../assets/mask.png);
            // background-repeat: no-repeat;
            // background-position: center;
            // background-size: contain;
            img {
              border-radius: 4px;
              width: 100%;
              height: 100%;
              max-width: 100%;
              border: none;
              font-size: 10px;
              background-image: url(../assets/mask.png);
              background-repeat: no-repeat;
              background-position: center;
              background-size: contain;
            }
            .cover-panel {
              position: absolute;
              left: 0;
              right: 0;
              top: 0;
              bottom: 0;
              .play-btn {
                position: absolute;
                max-width: 72px;
                max-height: 72px;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
              }
            }
          }
          .right-content {
            overflow: hidden;
            // padding-left:15px;
            .title {
              overflow: hidden;
              white-space: nowrap;
              text-overflow: ellipsis;
              font-size: 16px;
              line-height: 1.38;
              color: #000;
              text-align: left;
              font-weight: normal;
            }
            .sub-title {
              margin: 5px 0 7px;
              font-size: 13px;
              line-height: 1.38;
              //   line-height: 34px;
              color: #999;
              overflow: hidden;
              display: flex;
              display: -webkit-box;
              -webkit-line-clamp: 2;
              text-overflow: ellipsis;
              -webkit-box-orient: vertical;
              text-align: left;
            }
            .album-info {
              font-size: 11px;
              line-height: 1.45;
              white-space: nowrap;
              color: #999;
              text-align: left;

              & > span {
                margin-right: 10px;
                display: inline-block;
                line-height: 20px;
                vertical-align: top;
                min-width: 55px;
              }
              &:first-child {
                display: inline-block;
              }
            }
          }
        }
      }
    }
  }
}
</style>