<template>
  <div class="search">
    <section class="page-search">
      <div class="header">
        <form action="/">
          <van-search
            shape="round"
            v-model="value"
            show-action
            autofocus="true"
            @search="onSearch"
            @cancel="onCancel"
          />
        </form>
      </div>

      <section class="search-history">
        <p class="history-title" v-if="searchHistory.length">
          <span>搜索历史</span>
          <i class="xm-icon icon-delete F_F" @click="delHistory()"></i>
        </p>
        <p class="history-list" v-if="searchHistory.length">
          <a
            v-for="(item, index) in searchHistory"
            :key="index"
            @click="clickHandler"
            >{{ item }}</a
          >
        </p>

        <section class="records">
          <div class="tab-nav" v-if="category">
            <div class="nav-item">
              <van-tabs
                v-model="active"
                title-inactive-color="#a3a3ac"
                line-width="18px"
                swipeable
                class="selected_kw"
                @click="changeIndex(active)"
              >
                <!-- :item="item" -->
                <van-tab
                  :title="item.categoryName"
                  v-for="(item, index) in category"
                  :key="index"
                >
                  <!-- <SearchList :hotWordResultList="hotWordResultList" /> -->
                  <!-- <van-skeleton
                    title
                    animate
                    :row="10"
                    :loading="loading"
                    v-if="!hotWordResultList.length"
                  /> -->
                  <ul class="list">
                    <li v-for="(item, index) in hotWordResultList" :key="index" :item="item">
                      <span class="order">{{ index + 1 }}</span>
                      <span class="keyword" @click="clickHandler">{{
                        item.display_word
                      }}</span>
                      <i class="xm-icon iconRank rise XR_ icon-rise"></i>
                    </li>
                    <li class="mask">
                      <p>打开喜马拉雅App</p>
                      <p>可查看完整热搜榜</p>
                    </li>
                  </ul>
                </van-tab>
              </van-tabs>
            </div>
          </div>
          <!-- <ul class="list">
            <li v-for="(item, index) in hotWordResultList" :key="index">
              <span class="order">{{ index + 1 }}</span>
              <span class="keyword" @click="clickHandler">{{
                item.display_word
              }}</span>
              <i class="xm-icon iconRank rise XR_ icon-rise"></i>
            </li>
            <li class="mask">
              <p>打开喜马拉雅App</p>
              <p>可查看完整热搜榜</p>
            </li>
          </ul> -->
        </section>
      </section>

    </section>
    <div class="footer">
      <div class="logo"></div>
      <div class="open">
        <div class="content">
          <span>打开APP,听更多声音</span>
          <i class="xm-icon icon-album_btn_more"></i>
        </div>
      </div>

      <div class="copyright">
        <p>© 2014-2021 ximalaya.com 版权所有</p>
      </div>
      <p class="forbid">未经授权，禁止商用，喜马拉雅保留追究法律责任的权利</p>
    </div>
  </div>
</template>

<script>
// import { Toast } from 'vant';
// import SearchList from "../components/SearchList"
export default {
  // props: [],
  // components:{
  //   SearchList
  // },
  data: function () {
    return {
      active: 0,
      category: [],
      hotWordResultList: [],
      value: "",
      searchResultList: null,
      searchHistory: [],
      isShow: false,
      arr: [],
      loading: true,
    };
  },
  created: function () {
    // console.log(this.cate);
    this.axios
      .get("https://m.ximalaya.com/hotWordBillboardCategory")
      .then((res) => {
        this.category = res.data.category;
        this.arr = this.category.map((item) => {
          return item.categoryId;
        });
        // console.log(this.arr);
      });
    this.axios
      .get(
        "https://apis.netstart.cn/ximalaya/search/hotWordBillboardList?categoryId=-1&size=24"
      )
      .then((res) => {
        setTimeout(() => {
          this.hotWordResultList = res.data.hotWordResultList;
        }, 3000);
      });
  },
  mounted: function () {
    if (JSON.parse(window.localStorage.getItem("searchHistory"))) {
      this.searchHistory = JSON.parse(
        window.localStorage.getItem("searchHistory")
      );
    }
  },
  methods: {
    onSearch() {
      this.axios
        .get(
          "https://apis.netstart.cn/ximalaya/search?kw=" +
            this.value +
            "&core=all&page=1&rows=20"
        )
        .then((res) => {
          this.searchResultList = res.data.data.albumViews;
          // console.log(this.searchResultList);
        });
      //  var path = this.$route.fullPath.split("/");
      //  console.log(path[path.length-1]);
      this.$router.push({
        name: "SearchDetail",
        params: {
          kw: this.value,
        },
      });
      this.searchRecords();
    },
    onCancel() {
      this.value = "";
      this.$router.push("/home");
      this.$route.query.kw = "";
      // this.searchRecords();
    },
    searchRecords: function () {
      if (!this.searchHistory.includes(this.value)) {
        this.searchHistory.unshift(this.value);
        localStorage.setItem(
          "searchHistory",
          JSON.stringify(this.searchHistory)
        );
      } else {
        let newArr = this.searchHistory.indexOf(this.value);
        this.searchHistory.splice(newArr, 1);
        if (this.value !== "") {
          this.searchHistory.unshift(this.value);
        }
        localStorage.setItem(
          "searchHistory",
          JSON.stringify(this.searchHistory)
        );
      }
    },
    clickHandler: function (event) {
      this.value = event.target.innerText;
      this.onSearch();
      this.searchRecords();
      this.isShow = true;
    },
    delHistory: function () {
      window.localStorage.removeItem("searchHistory");
      this.searchHistory = "";
      this.isShow = false;
    },
    count: function (n) {
      return n > 100000000
        ? (n / 100000000).toFixed(2) + "亿"
        : (n / 10000).toFixed(2) + "万";
    },
    changeIndex: function (index) {
      this.active = index;
      // console.log(this.active,index);
      this.axios
        .get(
          "https://apis.netstart.cn/ximalaya/search/hotWordBillboardList?categoryId=" +
            this.arr[this.active] +
            "&size=24"
        )
        .then((res) => {
          this.hotWordResultList = res.data.hotWordResultList;
        });
    },
  },
};
</script>

<style lang="less" scoped>
@import "../assets/icon.css";
.search {
  // width: 100vw;
  // height: 100vh;
  // position: fixed;
  // left: 0;
  // top: 0;
  // background: skyblue;
  // z-index: 1000;
  .page-search {
    min-height: 100%;
    padding: 0 14px;
    .header {
      // padding: 8px 0;
      // display: flex;
      z-index: 101;
      .search-box {
        width: 100%;
        background: #eff1f4;
        height: 30px;
        border-radius: 30px;
        display: flex;
        align-items: center;
        .icon-searchlight {
          padding-left: 10px;
        }
        input {
          border: none;
          background: #eff1f4;
        }
      }
      .cancel {
        border: none;
        padding: 0 0 0 10px;
        font-size: 16px;
        color: #333;
        background-color: transparent;
        white-space: nowrap;
      }
    }
  }

  .search-history {
    display: block;
    .history-title {
      display: flex;
      -webkit-box-pack: justify;
      justify-content: space-between;
      padding: 10px 0 5px;
    }
    .history-list {
      text-align: left;
      a {
        display: inline-block;
        margin: 5px 10px 10px 0;
        padding: 5px 15px;
        max-width: 100%;
        line-height: 30px;
        background: #f3f4f5;
        border-radius: 4px;
        -webkit-box-sizing: border-box;
        box-sizing: border-box;
        color: #333;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
      }
    }

    .records {
      margin: 0 -14px;
      position: relative;
      min-height: 50vh;
      .tab-nav {
        // height: 45px;
        line-height: 45px;
        box-sizing: border-box;
        background: #fff;
        position: relative;
        overflow: hidden;
        width: 100%;
        .nav-item {
          display: flex;
          align-items: center;
        }
      }
      .list {
        margin-top: 10px;
        li {
          padding: 0 15px 0 27px;
          line-height: 45px;
          position: relative;
          display: flex;

          &:nth-last-of-type(2),
          &:nth-last-of-type(3),
          &:nth-last-of-type(4),
          &:nth-last-of-type(5) {
            -webkit-filter: blur(5px);
            filter: blur(5px);
          }

          .order {
            font-size: 16px;
            width: 20px;
            color: #dabda6;
          }
          &:nth-child(1) .order {
            color: #ff0b0b;
            font-size: 20px;
          }
          &:nth-child(2) .order {
            color: #f86442;
            font-size: 20px;
          }
          &:nth-child(3) .order {
            color: #f8a642;
            font-size: 20px;
          }
          .keyword {
            padding-left: 10px;
            font-size: 16px;
            color: #333;
            flex: 1;
            text-align: left;
            border-bottom: 1px solid #f3f4f5;
          }
          .icon-rise {
            color: #e44a3b;
            font-size: 10px;
          }
        }
        .mask {
          position: absolute;
          bottom: 0;
          left: 0;
          right: 0;
          height: 175px;
          background: rgba(0, 0, 0, 0.7);
          padding-top: 60px;
          box-sizing: border-box;
          display: block;
          p {
            line-height: 20px;
            font-size: 14px;
            color: #fff;
            text-align: center;
          }
          &:last-child p {
            padding-top: 20px;
          }
        }
      }
    }
  }

  .footer {
    border-top: 0.5px solid #eee;
    max-width: 640px;
    min-width: 320px;
    margin: auto;
    height: 315px;
    background: url(../assets/footer.png) no-repeat center;
    background-size: cover;

    .logo {
      width: 100%;
      height: 36px;
      background: url(../assets/footer_logo.png) no-repeat center;
      background-size: 282px 36px;
      margin: 34px auto 0;
    }
    .open {
      width: 300px;
      display: flex;
      justify-content: space-around;
      font-size: 12px;
      margin: 7px auto 10px;
      text-align: center;
      .content {
        width: 292px;
        height: 49px;
        border-radius: 25px;
        border: 1px solid #fa2800;
        color: #fa2800;
        span {
          font-size: 18px;
          font-weight: normal;
          color: #fa2800;
          display: inline-block;
          line-height: 50px;
          margin-right: 8px;
        }
        .icon-album_btn_more {
          font-size: 16px;
        }
      }
    }
    .copyright {
      padding-bottom: 10px;
      font-size: 12px;
      font-weight: 400;
      color: gray;
      line-height: 16px;
      text-align: center;
      margin-top: 15px;
    }
    .forbid {
      font-size: 12px;
      text-align: center;
      color: grey;
      line-height: 12px;
      margin-bottom: 20px;
    }
  }
}
</style>