<template>
  <div>
    <ul class="list-view">
      <li
        v-for="item in classic"
        :key="item.id"
        @click="$emit('jump-about-page', item)"
      >
        <div class="left" v-if="item.albumInfo">
          <img :src="'//imagev2.xmcdn.com/' + item.albumInfo.cover" alt="" />
        </div>
        <div class="right">
          <h3 class="tit">{{ item.albumInfo.title }}</h3>
          <p class="content">{{ item.albumInfo.customTitle }}</p>
          <div class="rantings">
            <span class="myname">
              <i class="xm-icon icon-ic_classify"></i>
              {{ item.pageUriInfo.categoryName }}</span
            >
            <span class="count">
              <i class="xm-icon icon-ic_play"></i>
              {{ count(item.statCountInfo.playCount) }}
            </span>
            <span class="desc">
              <i class="xm-icon icon-sound"></i>
              {{ item.statCountInfo.trackCount }}
            </span>
          </div>
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  props: ["classic", "albumdetails"],
  methods: {
    count: function (n) {
      return n > 100000000
        ? (n / 100000000).toFixed(2) + "亿"
        : (n / 10000).toFixed(2) + "万";
    },
  },
};
</script>

<style lang="less" scoped>
.list-view {
  li {
    display: flex;
    border-bottom: 1px solid #f3f4f5;
    padding: 15px 10px 15px;
    box-sizing: border-box;
    .left {
      width: 70px;
      height: 70px;
      box-sizing: content-box;
      padding: 0 15px 0 0;
      background-image: url(../assets/mask.png);
      background-repeat: no-repeat;
      background-size: contain;
      img {
        width: 100%;
        height: 100%;
        border-radius: 4px;
        max-width: 100%;
      }
    }
    .right {
      overflow: hidden;
      .tit {
        overflow: hidden;
        padding: 0;
        white-space: nowrap;
        text-overflow: ellipsis;
        font-size: 16px;
        line-height: 1.38;
        text-align: left;
      }
      .content {
        margin: 5px 0 7px;
        font-size: 13px;
        color: #999;
        line-height: 1.38;
        text-align: left;
      }
      .rantings {
        font-size: 11px;
        line-height: 1.45;
        white-space: nowrap;
        text-align: left;
        span {
          display: inline-block;
          min-width: 55px;
          line-height: 20px;
          vertical-align: top;
          margin-right: 10px;
          color: #999;
        }
      }
    }
  }
}
</style>