<template>
  <div class="play" :class="{ playing: playing }">
    <div class="top">
      <div class="header">
        <img src="../assets/footer_logo.png" alt="" />
        <div class="wrap">
          <a @click="$router.push('/download')">打开</a>
        </div>
      </div>
    </div>
    <div class="play-box" v-if="currentPlayProgram.trackInfo.cover">
      <div
        class="play-mask"
        :style="{
          backgroundImage:
            'url(' +
            'https://imagev2.xmcdn.com/' +
            currentPlayProgram.trackInfo.cover +
            ')',
        }"
      ></div>
      <div class="play-content">
        <div class="cover-container">
          <span class="arrow-left" @click="togglePrevProgram()"></span>
          <span class="arrow-right" @click="toggleNextProgram()"></span>
          <div class="track-cover">
            <img
              :src="'//imagev2.xmcdn.com/' + currentPlayProgram.trackInfo.cover"
              alt=""
            />
            <i
              class="player-sprite play-btn player-play _Ow"
              @click="togglePlay(!playing)"
            ></i>
            <div class="play-count">
              <i class="xm-icon icon-data"></i>
              {{ count(currentPlayProgram.statCountInfo.playCount) }}
            </div>
          </div>
        </div>
        <h1>
          <span>{{ currentPlayProgram.trackInfo.title }}</span>
        </h1>
        <p class="play-tools">
          <span class="init-code" @click.self="playBackRate">倍速播放</span>
          <van-popup
            v-model="show"
            round
            position="bottom"
            :style="{ height: '40%' }"
          >
            <!-- <select
              v-if="show"
              ref="selectEl"
              @change="setPlaySpeed"
              class="selectedEl"
            >
              <option value="0.5">0.5x</option>
              <input type="radio" />
              <option value="1.0" selected>1.0</option>
              <input type="radio" />
              <option value="1.5">1.5x</option>
              <input type="radio" />
              <option value="2.0">2x</option>
              <input type="radio" />
            </select> -->
            <van-radio-group v-model="radio">
              <van-cell-group @click="setPlaySpeed" ref="selectEl">
                <van-cell clickable title="0.5x" radio="0.5">
                  <template #right-icon>
                    <van-radio name="1" title="1.0x" />
                  </template>
                </van-cell>
                <van-cell clickable radio="1.0">
                  <template #right-icon>
                    <van-radio name="2" />
                  </template>
                </van-cell>
              </van-cell-group>
            </van-radio-group>
          </van-popup>
          <a href="#" class="init-code">下载收听</a>
          <a class="collection" @click="toggleFav" v-if="!isFav"></a>
          <a @click="toggleFav" v-else
            ><van-icon
              
              name="star"
              color="#f86442"
              size="22px"
              class="star"
          /></a>
          <!-- <svg t="1637979373208" class="icon" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="4639" width="20" height="2" v-if="show"><path d="M512.009505 25.054894l158.199417 320.580987 353.791078 51.421464L767.995248 646.579761l60.432101 352.365345-316.417844-166.354615-316.436854 166.354615 60.432101-352.365345L0 397.057345l353.791078-51.421464z" fill="#f86442" p-id="4640"></path></svg> -->
        </p>
        <audio
          :src="currentPlayProgram.trackInfo.playPath"
          controls
          loop
          style="height: 50px"
          ref="audio"
          @durationchange="onDuration"
          @timeupdate="onCurrentTime"
          @play="onPlay"
          @pause="onPause"
          @ended="onEnded"
        ></audio>
        <div class="progress-bar-wrap">
          <div class="progress-bar">
            <van-slider
              v-model="value"
              min="0"
              :max="duration"
              active-color="#f86442"
              @change="changeHandler"
            />
            <span class="currentTime" v-if="currentTime">{{ formatSeconds(currentTime) }}</span>
          </div>
          <span class="duration">{{ formatSeconds(duration) }}</span>
        </div>
        <div class="open-app" @click="$router.push('/download')">打开APP,完整收听</div>
      </div>
    </div>

    <div class="track-list" v-if="works.albumInfo.cover">
      <div class="list-view-header">
        <h2 class="list-view-title"></h2>
        <div class="list-view-content">
          <div class="list-meta">
            <a href="#" class="img-box">
              <img
                :src="'//imagev2.xmcdn.com/' + works.albumInfo.cover"
                alt=""
              />
            </a>
            <div>
              <h2 class="tit">{{ works.albumInfo.title }}</h2>
              <p class="album-desc">
                {{
                  count(works.statCountInfo.subscribeCount)
                }}订阅&nbsp;&nbsp;&nbsp;&nbsp;{{
                  works.statCountInfo.trackCount
                }}集
              </p>
            </div>
          </div>
        </div>
      </div>
      <ul class="list-view-body">
        <li
          v-for="item in currentPlayList.slice(0, 6)"
          :key="item.id"
          @click="
            $router.push({
              name: 'Play',
              params: {
                clusterCode: item.pageUriInfo.categoryCode,
                categoryCode: item.trackInfo.albumId,
                id: item.playInfo.id,
              },
            })
          "
        >
          <a class="track-link">
            <div class="list-track">
              <div class="list-track-wrap">
                <i class="xm-icon list-play center-v c9 _Qw icon-list_play"></i>
                <div class="contentWrap">
                  <p class="xm-title">{{ item.trackInfo.title }}</p>
                </div>
              </div>
            </div>
          </a>
        </li>
      </ul>
      <div class="list-view-footer">
        <div class="load" @click="onload">
          <p>
            加载更多
            <i class="xm-icon icon-arrow-down"></i>
          </p>
        </div>
      </div>
    </div>
    <div class="div"></div>
    <div class="list-view">
      <AboutSimilarAlbum :albums="albums"></AboutSimilarAlbum>
      <div class="change">
        <div class="btn">
          <i class="xm-icon v-m OM_ icon-btn_change"></i>
          <span>换一批</span>
        </div>
      </div>
    </div>
    <div class="div"></div>

    <div class="anchor-page">
      <h2>主播信息</h2>
      <div
        class="anchor-info"
        @click="
          $router.push({
            name: 'AnchorDetail',
            params: {
              id: worker.id,
            },
          })
        "
        v-if="worker.userInfo"
        v-on:click="$emit('check-anchor', worker)"
      >
        <a href="#">
          <img
            :src="'https://imagev2.xmcdn.com/' + worker.userInfo.logo"
            alt=""
          />
          <div class="icon vip-level rJ_">
            <span class="vip">V{{ worker.userInfo.anchorGrade }}</span>
          </div>
        </a>
        <div class="content">
          <h4>{{ worker.userInfo.nickname }}</h4>
          <p>
            <i class="xm-icon icon-anchor_fans"></i>
            <span>{{
              (worker.statCountInfo.followerCount / 10000).toFixed(2) + "万"
            }}</span>
          </p>
        </div>
        <a href="#" class="attention">
          <i class="xm-icon attention _dn icon-attention"></i>
          <p class="des">加关注</p>
        </a>
      </div>
      <ul class="list-container">
        <li v-for="con in myalbums" :key="con.id">
          <a href="#" class="cover" v-if="con.albumInfo">
            <div class="album-cover">
              <img :src="'//imagev2.xmcdn.com/' + con.albumInfo.cover" alt="" />
            </div>
            <div class="album-des">
              <p>{{ con.albumInfo.title }}</p>
            </div>
          </a>
        </li>
      </ul>
      <div class="enter">
        <p>
          进入主页
          <i class="xm-icon icon-arrow-right"></i>
        </p>
      </div>
    </div>
    <div class="div"></div>
    <div class="comment-page">
      <div class="stretch-box">
        <div class="comment-box">
          <div class="comment-header">
            <h2 class="comment-title"></h2>
            <h2 class="box-title">{{ comments.totalCount }}条评论</h2>
          </div>
          <ul class="comment-body">
            <li v-for="comment in comments.comments" :key="comment.id">
              <div class="xm-comment">
                <a href="#" class="avatar">
                  <img
                    :src="'https://fdfs.xmcdn.com/' + comment.smallHeader"
                    alt=""
                  />
                </a>
                <div class="xm-comment-box">
                  <h4 class="nickname">{{ comment.nickname }}</h4>
                  <p class="content">
                    {{ comment.content }}
                    <!-- <img :src="https://s1.xmcdn.com/css/img/face/d_huaxin.gif" alt=""> -->
                  </p>
                  <div class="answer">
                    <a href="#">回复</a>
                    <div class="time">{{ createdTime(comment.createdAt) }}</div>
                    <a href="#" class="like">
                      {{ comment.likes }}
                      <i class="xm-icon icon-zan bA_ icon-zan"></i>
                    </a>
                  </div>
                  <div
                    class="comment-replies"
                    v-for="(con, index) in comment.replies"
                    :key="index"
                  >
                    <p>
                      <span>{{ con.nickname }}回复@{{ con.pNickname }}:</span>
                      <span>{{ con.content }}</span>
                    </p>
                  </div>
                </div>
                <!-- <div class="comment-mask">
                  <p class="more">
                    <i class="xm-icon icon-more"></i>
                  </p>
                </div> -->
              </div>
            </li>
          </ul>
          <div class="comment-mask" v-if="isShow">
            <p class="more">
              <i class="xm-icon icon-more" @click="isShow = false"></i>
            </p>
          </div>
        </div>
      </div>
    </div>

    <div class="classic-ting">
      <div class="header">
        <h2>喜马拉雅经典必听</h2>
        <div class="tag-bar"></div>
      </div>
    </div>
    <AboutListView
      :classic="classic"
      @jump-about-page="$emit('jump-about-page', $event)"
    ></AboutListView>
    <p class="view-more" @click="$router.push('/download')">
      <a href="#"
        >查看更多
        <i class="xm-icon icon-arrow-right"></i>
      </a>
    </p>
    <div class="footer">
      <div class="logo"></div>
      <div class="open">
        <div class="content">
          <span>打开APP,听更多声音</span>
          <i class="xm-icon icon-album_btn_more"></i>
        </div>
      </div>

      <div class="copyright">
        <p>© 2014-2021 ximalaya.com 版权所有</p>
      </div>
      <p class="forbid">未经授权，禁止商用，喜马拉雅保留追究法律责任的权利</p>
    </div>
  </div>
</template>

<script>
import AboutSimilarAlbum from "@/components/AboutSimilarAlbum.vue";
import AboutListView from "@/components/AboutListView.vue";
export default {
  props: [
    "currentPlayProgram",
    "currentPlayProgramId",
    "playlist",
    "works",
    "albums",
    "worker",
    "myalbums",
    "classic",
  ],
  data: function () {
    return {
      currentTime: 0,
      duration: 0,
      value: this.currentTime,
      playing: false,
      inputing: false,
      show: false,
      comments: [],
      isShow: true,
      isFav: false,
      radio: 1,
      newArr: [],
    };
  },
  created: function () {
    this.axios
      .get(
        "https://m.ximalaya.com/m-revision/common/track/queryTrackCommentsFirstPage?trackId=" +
          this.$route.params.id +
          "&pageSize=10&page=1"
      )
      .then((res) => {
        // console.log(res);
        this.comments = res.data.data;
        // console.log(this.comments);
      });
  },
  mounted: function () {
    this.currentTime = this.$refs.audio.currentTime;
    if (JSON.parse(window.localStorage.getItem("myFav"))) {
      this.newArr = JSON.parse(window.localStorage.getItem("myFav"));
    }
  },
  components: { AboutSimilarAlbum, AboutListView },

  computed: {
    currentPlayList: function () {
      return this.playlist.filter(
        (item) => item.id !== this.currentPlayProgram.id
      );
    },
  },
  methods: {
    count: function (n) {
      return n > 10000 ? (n / 10000).toFixed(2) + "万" : n;
    },
    formatSeconds: function (time) {
      let secondTime = parseInt(time);
      let min = 0;
      let result = "";
      if (secondTime > 60) {
        min = parseInt(secondTime / 60);
        secondTime = parseInt(secondTime % 60);
        if (min > 60) {
          min = parseInt(min % 60);
        }
      }
      // padStart填充指定位数的字符串
      result = `${min
        .toString()
        .padStart(2, "0")}:${secondTime.toString().padStart(2, "0")}`;
      return result;
    },
    createdTime: function (t) {
      var date = new Date(t);
      var Y = date.getFullYear() + "-";
      var M =
        (date.getMonth() + 1 < 10
          ? "0" + (date.getMonth() + 1)
          : date.getMonth() + 1) + "-";
      var D = date.getDate() + " ";
      var h = date.getHours() + ":";
      var m = date.getMinutes() + ":";
      var s = date.getSeconds();
      return Y + M + D + h + m + s;
    },
    onCurrentTime: function () {
      this.value = this.$refs.audio.currentTime;
      this.currentTime = this.$refs.audio.currentTime;
      // console.log(this.value);
    },
    onDuration: function () {
      this.duration = this.$refs.audio.duration;
    },
    onPlay: function () {
      // this.$refs.audio.playing = true;
      this.playing = true;
    },
    onPause: function () {
      // this.$refs.audio.playing = false;
      this.playing = false;
    },
    onEnded: function () {
      this.currentTime = 0;
      this.onPlay();
    },
    playBackRate: function () {
      this.show = !this.show;
      // this.setPlaySpeed();
    },
    // 控制音频播放速度
    setPlaySpeed: function () {
      switch (this.$refs.selectEl.radio) {
        case "0.5":
          this.$refs.audio.playbackRate = 0.5;
          break;
        case "1.0":
          this.$refs.audio.playbackRate = 1.0;
          break;
        case "1.5":
          this.$refs.audio.playbackRate = 1.5;
          break;
        case "2.0":
          this.$refs.audio.playbackRate = 2.0;
          break;
      }
    },
    changeHandler: function (currentTime) {
      this.$refs.audio.currentTime = currentTime;
    },
    togglePlay: function (playing) {
      playing ? this.$refs.audio.play() : this.$refs.audio.pause();
      this.playing = !this.playing;
      // console.log(playing);
    },
    togglePrevProgram: function () {
      var prevProgram;
      var currentPlayProgramIndex;
      currentPlayProgramIndex = this.playlist.findIndex(
        (item) => item.id === this.currentPlayProgramId
      );
      var prevIndex =
        currentPlayProgramIndex === -1
          ? this.playlist.length - 1
          : currentPlayProgramIndex - 1;
      prevProgram = this.playlist[prevIndex];
      this.$emit("update:current-play-program", prevProgram);
    },
    toggleNextProgram: function () {
      var nextProgram;
      var currentPlayProgramIndex;
      currentPlayProgramIndex = this.playlist.findIndex(
        (item) => item.id === this.currentPlayProgramId
      );
      var nextIndex =
        currentPlayProgramIndex === this.playlist.length - 1
          ? 0
          : currentPlayProgramIndex + 1;
      nextProgram = this.playlist[nextIndex];
      this.$emit("update:current-play-program", nextProgram);
      // this.$emit('curr')
      // console.log(nextProgram);
      this.$router.push({
        name: "Play",
        params: {
          clusterCode: nextProgram.pageUriInfo.categoryCode,
          categoryCode: nextProgram.trackInfo.albumId,
          id: nextProgram.trackInfo.id,
        },
      });
      this.$refs.audio.src = nextProgram.trackInfo.playPath;
    },
    onload: function () {
      this.$router.push("/download");
    },
    toggleFav: function () {
      this.isFav = !this.isFav;
      if (!this.newArr.includes(this.currentPlayProgram.trackInfo.title)) {
        this.newArr.unshift(this.currentPlayProgram.trackInfo.title);
        localStorage.setItem(
          "myFav",
          JSON.stringify(this.newArr)
        );
      } else {
        let newArr = this.newArr.indexOf(this.currentPlayProgram.trackInfo.title);
        this.newArr.splice(newArr, 1);
        localStorage.setItem(
          "myFav",
          JSON.stringify(this.newArr)
        );
      }
    },
  },
  watch: {
    $route(to, from) {
      to, from;
    },
  },
};
</script>

<style lang="less" scoped>
@keyframes animate {
  from {
    transform: scale(1);
  }
  to {
    transform: scale(1.1);
  }
}
.play {
  height: 100vh;
  width: 100%;
  background: #f0f0f0;
  margin: auto;
  .top {
    width: 100%;
    position: static;
    // margin-top:-56px;
    background: rgba(34, 35, 37, 0.8);
    // height:50px;
    box-sizing: border-box;
    z-index: 999;
    .header {
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: 10px 8px 10px 8px;
      img {
        width: 282px;
        height: 36px;
        position: relative;
        top: 3px;
      }
      .wrap {
        a {
          width: 60px;
          background: #f86442;
          border: 1px solid #f86442;
          text-decoration: none;
          color: #fff;
          padding: 3px 0px;
          border-radius: 100px;
          text-align: center;
          display: block;
          line-height: 1.4;
        }
      }
    }
  }
  .play-box {
    height: 67vh;
    background: #28282d;
    position: relative;
    overflow: hidden;

    .play-mask {
      width: 300%;
      height: 300%;
      background-repeat: no-repeat;
      background-size: 100% 100%;
      background-position: center center;
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
      filter: blur(50px) brightness(0.8);
    }
    .play-content {
      position: relative;
      .cover-container {
        width: 130px;
        height: 130px;
        margin: 90px auto 0;
        position: relative;
        .arrow-left {
          display: block;
          right: 100%;
          margin-right: 27px;
          background-image: url(data:image/png;base64,);
          position: absolute;
          width: 36px;
          height: 36px;
          top: 50%;
          transform: translate(0, -50%);
          background-size: cover;
        }
        .arrow-right {
          display: block;
          left: 100%;
          margin-left: 27px;
          background-image: url(data:image/png;base64,);
          position: absolute;
          width: 36px;
          height: 36px;
          top: 50%;
          transform: translate(0, -50%);
          background-size: cover;
        }
        .track-cover {
          width: 130px;
          height: 130px;
          position: relative;
          border-radius: 4px;
          overflow: hidden;
          background-image: url(../assets/mask.png);
          background-size: contain;
          background-position: center;
          background-repeat: no-repeat;
          img {
            width: 100%;
            height: 100%;
            border-radius: 4px;
          }
          .player-sprite {
            overflow: hidden;
            background-image: url(//s1.xmcdn.com/yx/ximalaya-mobile-resource/last/dist/images/player-sprite_5ab8758.png);
            background-position: 0 -152px;
            background-repeat: no-repeat;
            background-size: 100% 300%;
            position: absolute;
            width: 76px;
            height: 76px;
            left: 0;
            top: 0;
            bottom: 0;
            right: 0;
            margin: auto;
            color: #fff;
            box-sizing: border-box;
          }
          .play-count {
            position: absolute;
            bottom: 0;
            right: 0;
            left: 0;
            text-align: right;
            padding: 4px 10px 4px 25px;
            font-size: 12px;
            line-height: 1.42;
            font-weight: 700;
            color: #fff;
            background-image: linear-gradient(
              90deg,
              rgba(0, 0, 0, 0) 0,
              rgba(0, 0, 0, 0.25) 100%
            );
          }
        }
      }
      h1 {
        margin: 20px 15px 12px;
        font-size: 20px;
        line-height: 1.4;
        font-weight: normal;
        color: #fff;
        span {
          vertical-align: middle;
        }
      }
      .play-tools {
        position: relative;
        text-align: center;
        .init-code {
          display: inline-block;
          padding: 2px 7px;
          background: rgba(65, 65, 65, 0.5);
          border: 1px solid #ffdf00;
          border-radius: 100px;
          font-size: 11px;
          color: #ffdf00;

          &:first-of-type {
            margin-right: 10px;
          }
        }
        .selectedEl {
          -webkit-appearance: none;
          -moz-appearance: none;
          appearance: none;
          border: none;
          background: #fff;
          option {
            background: #fff;
          }
        }

        .collection {
          position: absolute;
          top: 0;
          right: 0;
          width: 28px;
          height: 28px;
          margin-right: 20px;
          margin-top: -8px;
          background-image: url(data:image/png;base64,);
          background-size: cover;
          float: right;
        }
        .star {
          position: absolute;
          right: 23px;
          top: -5px;
        }
      }
      .open-app {
        display: block;
        width: 235px;
        margin: 0 auto;
        color: #fff;
        background-image: linear-gradient(-269deg, #ff8b70 2%, #f86442 99%);
        padding: 10px 0;
        border-radius: 100px;
        line-height: 1.4;
        box-sizing: border-box;
        margin-top: -30px;
        animation: animate linear 0.4s infinite 0s alternate;
        box-shadow: 0 0 5px 0 rgb(0 0 0 / 25%);
      }
      audio {
        // opacity: 0;
        display: none;
      }
      .progress-bar-wrap {
        position: relative;
        margin: 30px 35px 60px 35px;
        .progress-bar {
          position: relative;
          height: 2px;
          background-color: rgba(255, 255, 255, 0.3);
          .touch-box {
            position: absolute;
            left: 0;
            right: 0;
            top: 50%;
            height: 40px;
            transform: translate(0, -50%);
          }
          .bar {
            position: absolute;
            left: 0;
            height: 2px;
            width: 0%;
            background: #fff;
          }
          .played-progress {
            width: 0%;
            position: absolute;
            left: 0;
            height: 2px;
            background: #f86442;

            &::before {
              content: "";
              width: 22px;
              height: 22px;
              background-color: rgba(255, 255, 255, 0.3);
              position: absolute;
              right: 0;
              top: 50%;
              transform: translate(50%, -50%);
              border-radius: 50%;
            }

            &::after {
              content: "";
              position: absolute;
              right: 0;
              top: 50%;
              transform: translate(50%, -50%);
              border-radius: 50%;
