<template>
  <div class="anchor-detail">
    <div class="container">
      <div class="top">
        <div class="header">
          <img src="../assets/footer_logo.png" alt="" />
          <div class="wrap">
            <a href="#">打开</a>
          </div>
        </div>
      </div>
      <div class="banner-container">
        <van-skeleton
          title
          avatar
