                >V{{ details.anchorInfo.userInfo.anchorGrade }}</span
              >
            </div>
          </div>
          <div class="anchor-msg">
            <h1>{{ details.anchorInfo.userInfo.nickname }}</h1>
            <div class="shortIntro">
              <p style="text-align: left">
                简介:<span
                  v-if="details.anchorInfo.userInfo.personalSignature"
                  >{{ details.anchorInfo.userInfo.personalSignature }}</span
                >
                <span v-else>暂无</span>
              </p>
            </div>
            <div class="vertify">
              喜马认证:{{ details.anchorInfo.userInfo.personDescribe }}
            </div>
          </div>
        </div>
        <div class="data">
          <span class="count"
            >关注
            <span>{{ details.anchorInfo.statCountInfo.followingCount }}</span>
