          <van-tab title="资料">
            <AnchorInformation></AnchorInformation>
          </van-tab>
          <van-tab title="声音">
            <AnchorSound></AnchorSound>
          </van-tab>
        </van-tabs>
      </div>
    </div>
  </div>
</template>

<script>
import AnchorAlbum from "@/components/AnchorAlbum.vue";
import AnchorInformation from "@/components/AnchorInformation.vue";
import AnchorSound from "@/components/AnchorSound.vue";
export default {
  props: ["anchor"],
  components: { AnchorAlbum, AnchorInformation, AnchorSound },
  data: function () {
    return {
      active: 0,
      details: {},
    };
  },
  methods: {
    count: function (n) {
      return (n / 10000).toFixed(2) + "万";
    },
  },
  created: function () {
    this.axios
      .get(
        "https://apis.netstart.cn/ximalaya/anchor/queryAnchorPage/" +
          this.anchor +
          "?pageSize=20&tabType=2"
      )
      .then((res) => {
        this.details = res.data.data;
        // console.log(this.details);
      });
  },
};
</script>

