<template>
  <div class="album">
    <section class="cate">
      <ul class="selectTypeList">
        <li
          v-for="(item, index) in arr"
          :key="index"
          :class="{ current: active === index}"
          @click="changeIndex(index)"
          
        >
          <span class="text" ref="domEl">{{ item }}</span>
          <i class="xm-icon iconArrow qa_ icon-arrow-down"></i>
        </li>
      </ul>
    </section>

    <transition name="fade">
      <div class="selectContent" v-if="show" @click="show = false">
        <ul class="selectList">
          <li
            class="selectText"
            v-for="(cate, index) in category"
            :key="index"
            :class="{ selected: select === index }"
            @click="clickHandler(cate,index)"
          >
            {{ cate.categoryName }}
          </li>
        </ul>
      </div>
    </transition>
    <!-- 内容 -->
    <section class="content-page">
      <div class="scroll_wrapper">
        <div class="content">
          <section class="list-view">
            <ul>
              <li>
                <div class="itemWrapper">
                  <div
                    class="xm-album"
                    v-for="(item, index) in filters"
                    :key="index"
                  >
                    <div class="left-cover">
                      <img
                        :src="
                          item.albumInfo.cover_path
                            .split('!')
                            .splice(0, 1)
                            .toString()
                        "
                        alt=""
                      />
                      <div class="cover-panel">
                        <div class="mask"></div>
                        <div class="play-btn"></div>
                      </div>
                    </div>
                    <div class="right-content">
                      <h3 class="title">{{ item.albumInfo.title }}</h3>
                      <p class="sub-title">
                        {{ item.albumInfo.custom_title }}
                      </p>
                      <div class="album-info">
                        <span>
                          <i class="xm-icon icon-user"></i>
                          {{ item.albumInfo.nickname }}
                        </span>
                        <span>
                          <i class="xm-icon icon-sound"></i>
                          {{ item.albumInfo.tracks }}
                        </span>
                        <span>
                          <i class="xm-icon icon-data"></i>
                          {{ count(item.albumInfo.play) }}
                        </span>
                      </div>
                    </div>
                  </div>
                </div>
              </li>
            </ul>
          </section>
        </div>
      </div>
    </section>

    <div class="loading" v-if="isShow">
      <img
        src="data:image/svg+xml;base64,"
        alt=""
      />
    </div>

    <div class="footer">
      <div class="logo"></div>
      <div class="open">
        <div class="content">
          <span>打开APP,听更多声音</span>
          <i class="xm-icon icon-album_btn_more"></i>
        </div>
      </div>

      <div class="copyright">
        <p>© 2014-2021 ximalaya.com 版权所有</p>
      </div>
      <p class="forbid">未经授权，禁止商用，喜马拉雅保留追究法律责任的权利</p>
    </div>
  </div>
</template>

<script>
export default {
  props: ["value"],
  data: function () {
    return {
      show: false,
      arr: ["分类", "排序方式", "是否付费"],
      category: [],
      details: [],
      page: 1,
      isShow: false,
      active: "",
      select: 0,
      filters:[]
    };
  },
  // 专辑分类
  created: function () {
    this.axios
      .get(
        "https://m.ximalaya.com/m-revision/page/search/allCategory?kw=" +
          this.value
      )
      .then((res) => {
        this.category = res.data.data.categories;
      });

        this.axios
        .get(
          "https://m.ximalaya.com/m-revision/page/search?kw=" +
            this.value +
            "&core=album&page=1&rows=20&category_id=-1"
        )
        .then((res) => {
          this.filters = res.data.data.albumViews.albums;
        });
  },
  mounted: function () {
    this.isShow = false;
    this.getMoreAlbums();
    window.addEventListener("scroll", this.scrollBottom);
  },
  destroyed: function () {
    window.addEventListener("scroll", this.scrollBottom);
  },
  methods: {
    count: function (n) {
      return n > 100000000
        ? (n / 100000000).toFixed(2) + "亿"
        : (n / 10000).toFixed(2) + "万";
    },
    scrollBottom: function () {
      // 滚动高度
      let scrollTop =
        document.documentElement.scrollTop || document.body.scrollTop;
      // 盒子高度
      let windowHeight =
        document.documentElement.offsetHeight || document.body.offsetHeight;
      // 内容高度
      let scrollHeight =
        document.documentElement.scrollHeight || document.body.scrollHeight;
      if (scrollHeight - windowHeight + 100 <= scrollTop) {
        setTimeout(() => {
          this.getMoreAlbums();
          this.page += 20;
        }, 1500);
      }
    },
    getMoreAlbums: function () {
      //   专辑详情
      this.axios
        .get(
          "https://apis.netstart.cn/ximalaya/search?kw=" +
            this.value +
            "&core=album&page=" +
            this.page +
            "&rows=20"
        )
        .then((res) => {
          this.details = res.data.data.albumViews.albums;
        }, 2000);
    },
    changeIndex: function (index) {
      this.active = index;
      this.show = true;
    },
    clickHandler: function (cate,index) {
     this.$refs.domEl[0].innerText = cate.categoryName;
      this.select = index;
      this.axios
        .get(
          "https://m.ximalaya.com/m-revision/page/search?kw=" +
            this.value +
            "&core=album&page=1&rows=20&category_id="+cate.categoryId
        )
        .then((res) => {
          this.filters = res.data.data.albumViews.albums;
        });
    },
  },
};
</script>

<style lang="less" scoped>
.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.5s;
}
.fade-enter,
.fade-leave-to {
  opacity: 0;
}
.album {
  .cate {
    position: relative;
    z-index: 20;
    background: #fff;
    display: block;
    .selectTypeList {
      display: -webkit-box;
      display: flex;
      position: relative;
      li {
        position: relative;
        -webkit-box-flex: 1;
        flex: 1;
        padding: 0 10px;
        text-align: center;
        line-height: 40px;
        height: 40px;
        box-sizing: border-box;
        width: 80px;
        white-space: nowrap;
        &::before {
          content: "";
          position: absolute;
          left: 0;
          top: 0;
          bottom: 0;
          margin: auto;
          width: 1px;
          height: 20px;
          -webkit-transform: scale(0.5);
          -ms-transform: scale(0.5);
          transform: scale(0.5);
          background: #a3a3ac;
        }
        .text {
          display: inline-block;
          max-width: 80%;
          padding-right: 6px;
          font-size: 14px;
          color: #72727b;
          vertical-align: middle;
          overflow: hidden;
          white-space: nowrap;
          text-overflow: ellipsis;
        }
        .icon-arrow-down {
          display: inline-block;
          width: 10px;
          vertical-align: middle;
          font-size: 10px;
          color: #a3a3ac;
        }
      }
    }
  }

  .selectContent {
    position: absolute;
    left: 0;
    width: 100%;
    height: 100vh;
    background: rgba(0, 0, 0, 0.3);
    display: block;
    z-index: 11;
    .selectList {
      overflow: auto;
      max-height: 330px;
      padding: 0 15px;
      background: #fff;
      border-radius: 0 0 10px 10px;
      .selectText {
        background: rgba(254, 121, 89, 0.1);
        color: #fe7959;
        display: inline-block;
        margin: 15px 15px 0 0;
        padding: 5px 15px;
        line-height: 20px;
        background: #f3f4f5;
        border-radius: 4px;
        color: #666;
        font-size: 14px;
        text-align: center;
        list-style: none;
      }
    }
  }
  .content-page {
    // padding-top: 125px;
    z-index: 9;
    display: block;

    .scroll_wrapper {
      height: 100%;
      .content {
        width: 100%;
        height: 100%;
        box-sizing: border-box;
        position: relative;
        .list-view {
          background: #fff;
          position: relative;
          display: block;
          z-index: 10;
          height: 100%;
          transition: all 0.1s;
          ul {
            li {
              list-style: none;
              display: flex;
              margin-left: 15px;
              position: relative;
              .itemWrapper {
                width: 100px;
                -webkit-box-flex: 1;
                flex: 1;

                .xm-album {
                  padding: 15px 10px 15px 0;
                  box-sizing: border-box;
                  .left-cover {
                    width: 70px;
                    height: 70px;
                    float: left;
                    // padding: 0 15px 0 0;
                    background-color: #fff;
                    box-sizing: content-box;
                    margin-bottom: -1px;
                    position: relative;
                    max-height: 100%;
                    background-image: url(../../assets/mask.png);
                    background-repeat: no-repeat;
                    background-position: center;
                    background-size: contain;
                    img {
                      border-radius: 4px;
                      width: 100%;
                      height: 100%;
                      max-width: 100%;
                      border: none;
                      font-size: 10px;
                    }
                    .cover-panel {
                      position: absolute;
                      left: 0;
                      right: 0;
                      top: 0;
                      bottom: 0;
                      .play-btn {
                        position: absolute;
                        max-width: 72px;
                        max-height: 72px;
                        top: 50%;
                        left: 50%;
                        transform: translate(-50%, -50%);
                      }
                    }
                  }
                  .right-content {
                    overflow: hidden;
                    padding-left: 15px;
                    .title {
                      overflow: hidden;
                      white-space: nowrap;
                      text-overflow: ellipsis;
                      font-size: 16px;
                      line-height: 1.38;
                      color: #000;
                      text-align: left;
                      font-weight: normal;
                    }
                    .sub-title {
                      margin: 5px 0 7px;
                      font-size: 13px;
                      line-height: 1.38;
                      //   line-height: 34px;
                      color: #999;
                      overflow: hidden;
                      display: flex;
                      display: -webkit-box;
                      -webkit-line-clamp: 2;
                      text-overflow: ellipsis;
                      -webkit-box-orient: vertical;
                      text-align: left;
                    }
                    .album-info {
                      font-size: 11px;
                      line-height: 1.45;
                      white-space: nowrap;
                      color: #999;
                      text-align: left;

                      & > span {
                        margin-right: 10px;
                        display: inline-block;
                        line-height: 20px;
                        vertical-align: top;
                        min-width: 55px;
                      }
                      &:first-child {
                        display: inline-block;
                      }
                    }
                  }
                }
              }
            }
          }
        }
      }
    }
  }
  .loading {
    width: 100%;
    img {
      width: 50px;
      height: 50px;
      margin: auto;
    }
  }

  .footer {
    border-top: 0.5px solid #eee;
    max-width: 640px;
    min-width: 320px;
    margin: auto;
    height: 315px;
    background: url(../../assets/footer.png) no-repeat center;
    background-size: cover;

    .logo {
      width: 100%;
      height: 36px;
      background: url(../../assets/footer_logo.png) no-repeat center;
      background-size: 282px 36px;
      margin: 34px auto 0;
    }
    .open {
      width: 300px;
      display: flex;
      justify-content: space-around;
      font-size: 12px;
      margin: 7px auto 10px;
      text-align: center;
      .content {
        width: 292px;
        height: 49px;
        border-radius: 25px;
        border: 1px solid #fa2800;
        color: #fa2800;
        span {
          font-size: 18px;
          font-weight: normal;
          color: #fa2800;
          display: inline-block;
          line-height: 50px;
          margin-right: 8px;
        }
        .icon-album_btn_more {
          font-size: 16px;
        }
      }
    }
    .copyright {
      padding-bottom: 10px;
      font-size: 12px;
      font-weight: 400;
      color: gray;
      line-height: 16px;
      text-align: center;
      margin-top: 15px;
    }
    .forbid {
      font-size: 12px;
      text-align: center;
      color: grey;
      line-height: 12px;
      margin-bottom: 20px;
    }
  }
}
.album {
  .cate {
    .selectTypeList {
      .current {
        .iconArrow {
          color: #fe7959;
          display: inline-block;
          transform: rotate(180deg);
        }
        .text {
          color: #fe7959;
        }
      }
    }
  }
}

.album {
  .selectContent {
    .selectList {
        .selected {
          color: #fe7959;
          background: rgba(254, 121, 89, 0.1);
      }
    }
  }
}
</style>