<template>
  <div class="anchor">
    <section class="cate">
      <ul class="selectTypeList">
        <li class="selectType" v-for="(item, index) in arr" :key="index">
          <span class="selectText">{{ item }}</span>
          <!-- <i class="xm-icon iconArrow qa_ icon-arrow-down"></i> -->
        </li>
      </ul>
    </section>
    <section class="content">
      <div class="scroll-wrapper">
        <div class="container">
          <div class="list-view">
            <ul>
              <li
                class="resultItem"
                v-for="(item, index) in result"
                :key="index"
              >
                <div class="itemWrapper">
                  <div class="track">
                    <a class="track-wrap">
                      <div class="left-cover">
                        <img :src="item.userInfo.smallPic" alt="" />
                        <span class="play-btn-wrapper"></span>
                      </div>
                      <div class="right-content">
                        <h3 class="title">
                          <span>{{ item.userInfo.nickname }}</span>
                          <i class="grade LJ_ lv-11 _HZ"></i>
                        </h3>
                        <p
                          style="
                            color: #a3a3ac;
                            font-size: 12px;
                            line-height: 16px;
                            text-align: left;
                            margin-right: 5px;
                          "
                        >
                          {{ item.userInfo.personDescribe }}
                        </p>
                        <p class="desc">
                          <span>
                            <i class="xm-icon iconCount LJ_ icon-album"></i>
                            {{ item.userInfo.liveStatus }}
                          </span>
                          <span>
                            <i class="xm-icon iconCount LJ_ icon-sound"></i>
                            {{ item.userInfo.tracks_counts }}
                          </span>
                          <span>
                            <i class="xm-icon iconCount LJ_ icon-user"></i>
                            {{ count(item.userInfo.followers_counts) }}
                          </span>
                        </p>
                      </div>
                    </a>
