<script>
export default {
  props: ["value"],
  data: function () {
    return {
      arr: ["相关度", "最多声音", "最多粉丝"],
      result: [],
    };
  },
  created: function () {
    this.axios
      .get(
        "https://m.ximalaya.com/m-revision/page/search?kw=" +
          this.value +
          "&core=user&page=1&rows=20"
      )
      .then((res) => {
        this.result = res.data.data.userViews.users;
        // console.log(this.result)--      ;
      });
  },
  methods: {
    count: function (value) {
      return value > 10000 ? (value / 10000).toFixed(2) + "万" : value;
    },
  },
};
</script>

