<template>
  <div class="top">
    <div class="header">
      <div class="tab-nav">
        <van-tabs
          v-model="active"
          :active="active"
          color="#f06742"
          title-inactive-color="#a3a3ac"
          line-width="20px"
          @click="onClick"
        >
          <van-tab
            v-for="(item, index) in titles"
            :title="item.clusterName"
            :key="index"
            :to="{
              path: `/top/${item.clusterCode}/${item.categoryInfos[0].categoryCode}`,
            }"
          >
            <TopContent
              :content="content"
              :item="item"
              @send-value="$emit('send-value', $event)"
              @transfer-con="transferCon"
            ></TopContent>
          </van-tab>
        </van-tabs>
      </div>
    </div>
  </div>
</template>

<script>
// import { Notify } from 'vant';
import TopContent from "@/components/TopContent.vue";
export default {
  props: ["details"],
  components: { TopContent },
  data: function () {
    return {
      active: 0,
      titles: null,
      activeKey: 0,
      content: null,
    };
  },
  created: function () {
    //  头部导航栏
    this.axios
      .get("https://apis.netstart.cn/ximalaya/rank/queryRankClusterDetail")
      .then((res) => {
        this.titles = res.data.data;
      });
    // 内容部分
    // var arr =this.$route.path.split("/");
    // console.log(myparams);
    this.axios
      .get(
        "https://m.ximalaya.com/m-revision/page/rank/queryRank?clusterCode=" +
          this.$route.params.clusterCode +
          "&categoryCode=" +
          this.$route.params.categoryCode
      )
      .then((res) => {
        this.content = res.data.data;
        // console.log(res);
        // console.log(typeof(this.$route.params.categoryCode));
      });
  },
  methods: {
    count: function (n) {
      return (n / 100000000).toFixed(2) + "亿";
    },
    onClick: function (event) {
      console.log(event);
    },
    transferCon:function(e){
      this.$router.push({
        name:'Top',
        params:{
          clusterCode:this.$route.fullPath.split("/").splice(2,1).toString(),
          categoryCode:e
        }
      })
    // console.log(this.$route.path,e)
    }
  },
  // 监听路由变化,实现路由跳转,页面刷新
  watch: {
    $route(to, from) {
      to,from;
      this.axios
        .get(
          "https://m.ximalaya.com/m-revision/page/rank/queryRank?clusterCode=" +
            this.$route.params.clusterCode +
            "&categoryCode=" +
            this.$route.params.categoryCode
        )
        .then((res) => {
          this.content = res.data.data;
          // console.log(res);
          // console.log(typeof(this.$route.params.categoryCode));
        });
    },
  },
};
</script>

<style lang="less" scoped>
.top {
  height: 100vh;

  .header {
    height: 50px;
    line-height: 50px;
    z-index: 20;
    position: fixed;
    left: 0;
    top: 50px;
    z-index: 999;
    .tab-nav {
      height: 50px;
      line-height: 50px;
      width: 100%;
      position: relative;
      background: #fff;
      box-sizing: border-box;

      .current {
        font-size: 20px;
      }
    }
  }
}
</style>