<template>
  <div class="anchor-sound">
    <div class="list-container">
      <li class="list-view-item">
        <div
          class="xm-track"
          v-for="item in sound.trackDetailInfos"
          :key="item.id"
        >
          <a class="xm-track-wrap">
            <div class="img-cover">
              <img
                :src="'https://imagev2.xmcdn.com/' + item.trackInfo.cover"
                alt=""
              />
              <span class="play-btn-wrap">
                <van-icon name="play" color="#fff" size="24px" class="icon" />
              </span>
            </div>
            <div class="xm-track-content">
              <h3 class="title">{{ item.trackInfo.title }}</h3>
              <p class="xm-track-desc">
                <span>
                  <i class="xm-icon icon-ic_play"></i>
                  {{ count(item.statCountInfo.playCount) }}
                </span>
                <span>
                  <i class="xm-icon icon-commentlight"></i>
                  {{ count(item.statCountInfo.commentCount) }}
                </span>
                <span>
                  <i class="xm-icon icon-timelight"></i>
                </span>
                <span></span>
              </p>
            </div>
          </a>
        </div>
      </li>
    </div>

    <div class="loading" v-if="show">
      <img
        src="data:image/svg+xml;base64,"
        alt=""
      />
    </div>
    <div class="footer">
      <div class="logo"></div>
      <div class="open">
        <div class="content">
          <span>打开APP,听更多声音</span>
          <i class="xm-icon icon-album_btn_more"></i>
        </div>
      </div>

      <div class="copyright">
        <p>© 2014-2021 ximalaya.com 版权所有</p>
      </div>
      <p class="forbid">未经授权，禁止商用，喜马拉雅保留追究法律责任的权利</p>
    </div>
  </div>
</template>

<script>
export default {
  data: function () {
    return {
      sound: Object,
      pageSize: 20,
      show: false,
    };
  },
  created: function () {
    this.show = true;
  //   this.axios
  //     .get(
  //       "https://apis.netstart.cn/ximalaya/anchor/queryAnchorPage/" +
  //         this.$route.params.id +
  //         "?pageSize="+this.pageSize+"&tabType=1"
  //     )
  //     .then((res) => {
  //       this.sound = res.data.data.anchorTrackPageRecords;
  //       // console.log(this.sound);
  //     });
  },
  mounted: function () {
    this.getSound();
    this.show = false;
    window.addEventListener("scroll", this.scrollBottom);
  },
  destroyed: function () {
    window.addEventListener("scroll", this.scrollBottom);
  },
  methods: {
    count: function (value) {
      return value > 10000 ? (value / 10000).toFixed(2) + "万" : value;
    },
    scrollBottom: function () {
      // 滚动高度
      let scrollTop =
        document.documentElement.scrollTop || document.body.scrollTop;
      // 盒子高度
      let windowHeight =
        document.documentElement.offsetHeight || document.body.offsetHeight;
      // 内容高度
      let scrollHeight =
        document.documentElement.scrollHeight || document.body.scrollHeight;
      if (scrollHeight - windowHeight + 100<= scrollTop) {
        setTimeout(() => {
          this.pageSize += 6;
          this.getSound();
        },1500) 
        this.show = true;
      }
    },
    getSound: function () {
      this.axios
        .get(
          "https://apis.netstart.cn/ximalaya/anchor/queryAnchorPage/" +
            this.$route.params.id +
            "?pageSize=" +
            this.pageSize +
            "&tabType=1"
        )
        .then((res) => {
          setTimeout(() => {
            this.sound = res.data.data.anchorTrackPageRecords;
            // console.log(this.sound);
          });
        }, 2000);
    }
  },
};
</script>

<style lang="less" scoped>
.anchor-sound {
  .list-container {
    padding-top: 0;
    width: 96%;
    height: auto;
    margin-left: 4%;
    .list-view-item {
      list-style: none;
      border-bottom: 1px solid #f3f4f5;
      padding-right: 15px;
      .xm-track {
        padding: 15px 0;
        .xm-track-wrap {
          padding-right: 0;
          display: flex;
          position: relative;
          box-sizing: border-box;
          .img-cover {
            flex-shrink: 0;
            width: 44px;
            height: 44px;
            margin-right: 10px;
            overflow: hidden;
            border-radius: 50%;
            float: left;
            position: relative;
            img {
              width: 100%;
              height: 100%;
              max-width: 100%;
            }
            .play-btn-wrap {
              position: absolute;
              left: 0;
              top: 0;
              bottom: 0;
              right: 0;
              width: 100%;
              height: 100%;
              background: rgba(0, 0, 0, 0.3);
              text-align: center;
              .icon {
                position: absolute;
                left: 50%;
                top: 50%;
                transform: translate(-50%, -50%);
              }
            }
          }
        }
        .xm-track-content {
          -webkit-box-flex: 1;
          flex-grow: 1;
          flex-shrink: 1;
          overflow: hidden;
          .title {
            margin-bottom: 5px;
            font-size: 16px;
            font-weight: normal;
            line-height: 22px;
            max-height: 44px;
            color: #40404c;
            overflow: hidden;
            white-space: nowrap;
            text-overflow: ellipsis;
            text-align: left;
          }
          .xm-track-desc {
            font-size: 12px;
            line-height: 1.5;
            text-align: left;
            color: #999;
            span {
              margin-right: 15px;
              i {
                font-size: 10px;
              }
            }
          }
        }
      }
    }
  }

  .loading {
    width: 100%;
    img {
      width: 50px;
      height: 50px;
      margin: auto;
    }
  }
  .footer {
    border-top: 0.5px solid #eee;
    max-width: 640px;
    min-width: 320px;
    margin: auto;
    height: 315px;
    background: url(../assets/footer.png) no-repeat center;
    background-size: cover;

    .logo {
      width: 100%;
      height: 36px;
      background: url(../assets/footer_logo.png) no-repeat center;
      background-size: 282px 36px;
      margin: 34px auto 0;
    }
    .open {
      width: 300px;
      display: flex;
      justify-content: space-around;
      font-size: 12px;
      margin: 7px auto 10px;
      text-align: center;
      .content {
        width: 292px;
        height: 49px;
        border-radius: 25px;
        border: 1px solid #fa2800;
        color: #fa2800;
        span {
          font-size: 18px;
          font-weight: normal;
          color: #fa2800;
          display: inline-block;
          line-height: 50px;
          margin-right: 8px;
        }
        .icon-album_btn_more {
          font-size: 16px;
        }
      }
    }
    .copyright {
      padding-bottom: 10px;
      font-size: 12px;
      font-weight: 400;
      color: gray;
      line-height: 16px;
      text-align: center;
      margin-top: 15px;
    }
    .forbid {
      font-size: 12px;
      text-align: center;
      color: grey;
      line-height: 12px;
      margin-bottom: 20px;
    }
  }
}
</style>