<script>
import AboutSimilarAlbum from "@/components/AboutSimilarAlbum.vue";
import AboutListView from "@/components/AboutListView.vue";
import ProgramList from "@/components/ProgramList.vue";
export default {
  components: { AboutSimilarAlbum, AboutListView, ProgramList },
  props: ["details", "myworks","anchor"],
  data: function () {""
    return {
      albums: [],
      works: {},
      worker: {},
      show: false,
      programs: {},
      myalbums: {},
      classic: {},
      more: {},
      page:1
    };
  },
  created: function () {
    // var id = this.$route.path.split("/");
    // // 专辑详情
    this.getAlbums();

    // 作者
    this.getWorker();

    // 相似专辑
    this.getAlbum(1);

    // 主播作品
    this.getMyblums();

    // 节目列表
    this.getProgram();

    // 喜马拉雅经典必听
    this.getClassic();
  },
  methods: {
    count: function (n) {
      return n > 100000000
        ? (n / 100000000).toFixed(2) + "亿"
        : (n / 10000).toFixed(2) + "万";
    },
    describeCount: function (value) {
      return "已订阅" + (value / 10000).toFixed(2) + "万人";
    },
    clickHandler: function () {
      this.show = !this.show;
    },
    changeAlbums: function (event) {
      console.log(event);
    },
    currentPlayProgram: function (id) {
      this.$router.push({
        name: "About",
        params: {
          id,
        },
      });
    },
    // 加载更多
    onLoad: function () {
      var id = this.$route.path.split("/");
      this.axios
        .get(
          "https://m.ximalaya.com/m-revision/common/album/queryAlbumTrackRecordsByPage?albumId=" +
            id[id.length - 1] +
            "&page=2&pageSize=10&asc=true&countKeys=play%2Ccomment&v=1637399595180"
        )
        .then((res) => {
          this.more = res.data.data.trackDetailInfos;
          // console.log(res);
        });
    },
    // 专辑详情
    getAlbums: function () {
      var id = this.$route.path.split("/");
      // 专辑详情
      this.axios
        .get(
          "https://apis.netstart.cn/ximalaya/album/queryAlbumPage/" +
            id[id.length - 1]
        )
        .then((res) => {
          this.works = res.data.data.albumDetailInfo;
          this.$emit("transfer-works", this.works);
        });
    },
    // 作者
    getWorker: function () {
      this.axios
        .get(
          "https://m.ximalaya.com/m-revision/common/user/queryUserInfo/"+this.anchor+"?userCountKeys=follower"
        )
        .then((res) => {
          this.worker = res.data.data;
          // console.log(this.worker);
          this.$emit("transfer-worker", this.worker);
        });
    },
    // 主播作品
    getMyblums: function () {
      this.axios
        .get(
          "https://m.ximalaya.com/m-revision/common/anchor/queryAnchorAlbumsByPage?anchorId="+this.anchor+"&page=1&pageSize=3&asc=false"
        )
        .then((res) => {
          this.myalbums = res.data.data.albumBriefDetailInfos;
          // console.log(this.myalbums);
          this.$emit("transfer-myalbums", this.myalbums);
        });
    },
    // 节目列表
    getProgram: function () {
      var id = this.$route.path.split("/");
      this.axios
        .get(
          "https://m.ximalaya.com/m-revision/common/album/queryAlbumTrackRecordsByPage?albumId=" +
            id[id.length - 1] +
            "&page=1&pageSize=10&asc=true&countKeys=play%2Ccomment&v=1637399595180"
        )
        .then((res) => {
          this.programs = res.data.data.trackDetailInfos;
          this.$emit("update:playlist", this.programs);
          // console.log(this.programs);
        });
    },
    // 喜马拉雅经典必听
    getClassic: function () {
      this.axios
        .get(
          "https://m.ximalaya.com/m-revision/common/album/queryClassicListenAlbums"
        )
        .then((res) => {
          this.classic = res.data.data;
          // console.log(this.classic);
          this.$emit("transfer-classic", this.classic);
        });
      this.getAlbum(1);
    },
    // 相似专辑
    getAlbum: function (page) {
      var id = this.$route.path.split("/");
      this.axios
        .get(
          "https://m.ximalaya.com/m-revision/common/album/queryAlbumRelatedRecommendAlbums?albumId=" +
            id[id.length - 1] +
            "&"+page+"&pageSize=6"
        )
        .then((res) => {
          this.albums = res.data.data.albumBriefDetailInfos;
          // console.log(this.albums);
          this.$emit("transfer-albums", this.albums);
        });
    },
    // 更新专辑
    // updateAlbums: function () {
    //   var id = this.$route.path.split("/");
    //   this.axios
    //     .get(
    //       "https://m.ximalaya.com/m-revision/common/album/queryAlbumRelatedRecommendAlbums?albumId=" +
    //         id[id.length - 1] +
    //         "&page=2&pageSize=6"
    //     )
    //     .then((res) => {
    //       this.albums = res.data.data.albumBriefDetailInfos;
    //       // console.log(this.albums);
    //     });
    // },
  },
  watch: {
    $route(to, from) {
      to, from;
      // this.onLoad();
      this.getAlbums();
      this.getAlbum();
      this.getWorker();
      this.getProgram();
      this.getClassic();
      this.getMyblums();
    },
  },
};
</script>

