<template>
  <div class="top-content">
    <section class="menu-list">
      <ul class="left">
        <!-- 左边导航栏 -->
        <van-sidebar v-model="activeKey">
          <van-sidebar-item
            :title="tit.title"
            v-for="(tit, index) in item.categoryInfos"
            :key="index"
            line-height="10px;"
            @click="$emit('transfer-con', tit.categoryCode)"
          />
        </van-sidebar>
      </ul>
      <!-- 导航栏对应内容 -->
      <div class="right">
        <div class="content">
          <section class="list">
            <ul v-if="content">
              <li
                v-for="(con, index) in content.rankModuleInfoList"
                :key="con.id"
                @click="
                  $emit('send-value', {
                    categoryCode: con.pageUriInfo.categoryCode,
                    id: con.pageUriInfo.id,
                  })
                "
              >
                <span class="icon">{{ index + 1 }}</span>
                <div class="album-info" v-if="con.albumInfo">
                  <div class="img-box">
                    <img
                      :src="'https://imagev2.xmcdn.com/' + con.albumInfo.cover"
                      alt=""
                    />
                  </div>
                  <div class="content-box">
                    <h3 class="title">{{ con.albumInfo.title }}</h3>
                    <p class="des">{{ con.albumInfo.salePoint }}</p>
                    <div class="icon-box">
                      <span>
                        <i class="xm-icon icon-ic_play"></i>
                        {{ count(con.statCountInfo.playCount) }}
                      </span>
                      <span>
                        <i class="xm-icon icon-sound"></i>
                        {{ con.statCountInfo.trackCount }}
                      </span>
                    </div>
                  </div>
                </div>
              </li>
            </ul>
          </section>
        </div>
      </div>
    </section>
  </div>
</template>

<script>
export default {
  props: ["content", "item"],
  data:function(){
    return {
      activeKey:0
    }
  },
  methods: {
    count: function (n) {
      //       return (n / 100000000).toFixed(2) + "亿";
      return n / 10000
        ? (n / 100000000).toFixed(2) + "亿"
        : (n / 10000).toFixed(2) + "万";
    },
    clickHandler: function () {
      console.log(111);
    },
  },
};
</script>

<style lang="less" scoped>
.menu-list {
  background: #f3f4f5;
  display: flex;
  box-sizing: border-box;
  // margin-top: 100px;
  height: calc(100vh - 100px);

  .left {
    width: 90px;
    overflow-y: auto;
    overflow-x: hidden;
    &::-webkit-scrollbar {
      display: none;
    }
  }
  .right {
    width: 100%;
    height: 100%;
    .content {
      width: 100%;
      height: 100%;
      box-sizing: border-box;
      .list {
        height: 100%;
        overflow: hidden auto;
        background: #fff;
        display: block;
        position: relative;
        z-index: 10;

        ul {
          li {
            display: flex;
            .icon {
              margin: 18px 0;
              height: 19px;
              line-height: 19px;
              flex: 0 0 34px;
              color: #40404c;
              font-size: 16px;
              font-weight: 600;
              display: block;
            }
            &:first-child .icon {
              background: url(data:image/png;base64,)
                no-repeat;
            }

            &:nth-child(2) .icon {
              background: url(data:image/png;base64,)
                no-repeat;
            }

            &:nth-child(3) .icon {
              background: url(data:image/png;base64,)
                no-repeat;
            }
            &:not(:nth-child(n + 4)) .icon {
              background-size: contain;
              background-position: center;
              font-size: 0;
            }

            .album-info {
              flex: 1;
              width: 100px;
              position: relative;
              box-sizing: border-box;
              padding: 15px 10px 15px 0;

              .img-box {
                width: 70px;
                height: 70px;
                float: left;
                // padding: 0 15px 0 0;
                margin: 0 15px 0 0;
                background-color: #fff;
                -webkit-box-sizing: content-box;
                box-sizing: content-box;
                margin-bottom: -1px;
                background-image: url(../assets/mask.png);
                background-repeat: no-repeat;
                background-size: contain;
                background-position: center;
                img {
                  width: 100%;
                  height: 100%;
                  border-radius: 4px;
                }
              }
              .content-box {
                overflow: hidden;
                text-align: left;
                .title {
                  color: rgb(64, 64, 76);
                  font-size: 16px;
                  font-weight: 500;
                  line-height: 1.38;
                  display: -webkit-box;
                  -webkit-line-clamp: 2;
                  text-overflow: ellipsis;
                  -webkit-box-orient: vertical;
                  overflow: hidden;
                  text-align: left;
                }
                .des {
                  color: #999;
                  line-height: 1.38;
                  margin: 5px 0 7px;
                  margin-bottom: 10px;
                  font-size: 13px;
                  text-align: left;
                  text-overflow: ellipsis;
                  overflow: hidden;
                  white-space: nowrap;
                }
                .icon-box {
                  line-height: 1.45;
                  font-size: 11px;
                  white-space: nowrap;
                  color: #999;
                  span {
                    margin-right: 10px;
                    display: inline-block;
                    line-height: 20px;
                    vertical-align: top;
                    min-width: 55px;
                    text-align: left;

                    .icon-ic_play {
                      margin-right: 5px;
                      vertical-align: baseline;
                    }
                  }
                }
              }
            }
          }
        }
      }
    }
  }
}
</style>