<template>
  <div class="home-content">
    <div class="content-box">
      <!-- 分类(听单,排行榜,分类) -->
      <div class="content-wrap" v-if="tomatoes">
        <a
          v-for="item in tomatoes"
          :key="item.order"
          class="con"
          v-lazy="item"
          @click="
            $router.push({
              name: 'Top',
              params: {
                clusterCode:
                  item.link.split('/').splice(2, 1).toString() ||
                  item.link.split('/').splice(1, 1).toString(),
                categoryCode:
                  item.link.split('/').splice(3, 1).toString() ||
                  item.link.split('/').splice(2, 1).toString(),
              },
            })
          "
        >
          <img :src="item.img" alt="" />
          <a>{{ item.name }}</a>
        </a>
      </div>
    </div>

    <a href="#" class="advertise">
      <img src="../assets/ximalaya.png" alt="" />
    </a>
    <!-- 选项卡标题 -->
  <!-- <Loading v-if="!details.length"></Loading> -->
    <div class="nav-list" v-for="detail in details" :key="detail.id">
      <div class="list-header">
        <h2>{{ detail.title }}</h2>
        <a
          @click="
            $router.push({
              name: 'Top',
              params: {
                clusterCode: detail.rankingContentInfoList.clusterCode,
                categoryCode: detail.rankingContentInfoList.categoryCode,
              },
            })
          "
          >更多</a
        >
        <i class="xm-icon icon-album_btn_more"></i>
      </div>
      <!-- 选项卡里面的内容 -->
      <!-- <van-skeleton
        title
        avatar
        avatar-size="70px"
        :row="2"
        avatar-shape="square"
        :loading="loading"
        v-if="!detail.rankingContentInfoList.rankModuleInfoList.length"
      > -->
      
      <ul
        v-for="card in detail.rankingContentInfoList.rankModuleInfoList"
        :key="card.id"
      >
        <!-- <div>实际内容</div> -->
        <li
          class="rank-list"
          @click="
            $emit('transfer-value', {
              categoryCode: card.pageUriInfo.categoryCode,
              id: card.pageUriInfo.id,
            })
          "
          v-on:click="$emit('transfer-anchor', card.anchorInfo.id)"
        >
          <div class="left">
            <img :src="'//imagev2.xmcdn.com/' + card.albumInfo.cover" alt="" />
            <div class="xm-album-cover">
              <div class="mask"></div>
              <div class="play-btn"></div>
            </div>
          </div>
          <div class="right">
            <h3>{{ card.albumInfo.title }}</h3>
            <p class="des">{{ card.albumInfo.salePoint }}</p>
            <div class="album-info">
              <i class="xm-icon icon-sound"></i>&nbsp;
              <span class="trackCount">{{
                card.statCountInfo.trackCount
              }}</span>
              <i class="xm-icon icon-data"></i>&nbsp;
              <span class="playCount">{{
                count(card.statCountInfo.playCount)
              }}</span>
            </div>
          </div>
        </li>
      </ul>
      <!-- </van-skeleton> -->
    </div>

    <div class="footer">
      <div class="logo"></div>
      <div class="open">
        <div class="content">
          <span>打开APP,听更多声音</span>
          <i class="xm-icon icon-album_btn_more"></i>
        </div>
      </div>

      <div class="copyright">
        <p>© 2014-2021 ximalaya.com 版权所有</p>
      </div>
      <p class="forbid">未经授权，禁止商用，喜马拉雅保留追究法律责任的权利</p>
    </div>
  </div>
</template>

<script>
// import Loading from "@/components/Loading.vue";
export default {
  // components:{Loading},
  props: ["details", "tomatoes", "cards"],
  data: function () {
    return {
      loading: true,
    };
  },
  methods: {
    count: function (n) {
      return n > 100000000
        ? (n / 100000000).toFixed(2) + "亿"
        : (n / 10000).toFixed(2) + "万";
    },
  },
};
</script>

<style lang="less" scoped>
.home-content {
  overflow: hidden;

  .content-box {
    margin-top: 25px;
    // background: skyblue;

    .content-wrap {
      width: 310px;
      margin: 0 auto;
      display: flex;
      .con {
        flex: 1;
        display: flex;
        flex-direction: column;
        align-items: center;
        img {
          width: 34px;
          height: 34px;
          display: inline-block;
        }
        a {
          text-align: center;
          text-decoration: none;
          font-size: 12px;
          color: #666;
          text-align: center;
          height: 17px;
          line-height: 17px;
        }
      }
    }
  }
}
.advertise {
  color: #7e8c8d;
  text-decoration: none;

  img {
    max-width: 100%;
    border: none;
    font-size: 10px;
    margin: 18px 0 8px 0;
    overflow: hidden;
  }
}

.nav-list {
  padding: 15px;

  .list-header {
    position: relative;
    display: flex;

    h2 {
      font-size: 18px;
      line-height: 1.4;
      font-weight: bold;
      display: inline-block;
    }
    a {
      color: rgb(114, 114, 123);
      position: absolute;
      right: 14px;
      top: 0;
      height: 25px;
      line-height: 25px;
      text-decoration: none;
    }
    .icon-album_btn_more {
      width: 8px;
      height: 8px;
      position: absolute;
      right: 4px;
      top: 4px;
      color: rgb(114, 114, 123);
    }
  }

  .van-skeleton {
    display: -webkit-box;
    display: -webkit-flex;
    display: flex;
    padding: 0 0px;
    margin-top: 10px;
  }

  ul {
    // background: pink;
    .rank-list {
      border-bottom: 1px solid #f3f4f5;
      // display: flex;
      padding: 15px 10px 15px 0;
      box-sizing: border-box;

      .left {
        width: 70px;
        height: 70px;
        padding: 0 15px 0px 0;
        float: left;
        background-color: #fff;
        box-sizing: content-box;
        margin-bottom: -1px;
        // display: block;
        max-height: 100%;
        position: relative;
        background-image: url(../assets/mask.png);
        background-size: contain;
        background-repeat: no-repeat;
        border-radius:4px;

        img {
          width: 100%;
          height: 100%;
          border-radius: 4px;
          max-width: 100%;
        }
        .xm-album-cover {
          position: absolute;
          left: 0;
          top: 0;
          right: 0;
          bottom: 0;

          .play-btn {
            position: absolute;
            max-width: 72px;
            max-height: 72px;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            cursor: pointer;
          }
        }
      }
      .right {
        overflow: hidden;
        h3 {
          font-size: 16px;
          color: #333;
          font-weight: normal;
          white-space: nowrap;
          text-overflow: ellipsis;
          overflow: hidden;
          text-align: left;
        }

        .des {
          margin: 5px 0 7px;
          line-height: 1.38;
          font-size: 13px;
          color: #999;
          text-align: left;
        }
        .album-info {
          font-size: 11px;
          line-height: 1.45;
          text-overflow: nowrap;
          white-space: nowrap;
          text-align: left;
          i {
            vertical-align: baseline;
            color: #999;
          }
          span {
            display: inline-block;
            display: inline-block;
            line-height: 20px;
            vertical-align: top;
            min-width: 55px;

            color: #999;
            &:first-child {
              margin-right: 10px;
            }
          }
        }
      }
    }
  }
}

.footer {
  border-top: 0.5px solid #eee;
  max-width: 640px;
  min-width: 320px;
  margin: auto;
  height: 315px;
  background: url(../assets/footer.png) no-repeat center;
  background-size: cover;

  .logo {
    width: 100%;
    height: 36px;
    background: url(../assets/footer_logo.png) no-repeat center;
    background-size: 282px 36px;
    margin: 34px auto 0;
  }
  .open {
    width: 300px;
    display: flex;
    justify-content: space-around;
    font-size: 12px;
    margin: 7px auto 10px;
    text-align: center;
    .content {
      width: 292px;
      height: 49px;
      border-radius: 25px;
      border: 1px solid #fa2800;
      color: #fa2800;
      span {
        font-size: 18px;
        font-weight: normal;
        color: #fa2800;
        display: inline-block;
        line-height: 50px;
        margin-right: 8px;
      }
      .icon-album_btn_more {
        font-size: 16px;
      }
    }
  }
  .copyright {
    padding-bottom: 10px;
    font-size: 12px;
    font-weight: 400;
    color: gray;
    line-height: 16px;
    text-align: center;
    margin-top: 15px;
  }
  .forbid {
    font-size: 12px;
    text-align: center;
    color: grey;
    line-height: 12px;
    margin-bottom: 20px;
  }
}
</style>